import * as React from "react";

import RenExSDK, { AtomicConnectionStatus } from "@renex/renex";

import { connect } from "react-redux";
import { Dispatch } from "redux";
import { bindActionCreators } from "redux";

import Loading from "./Loading";

import { authorizeSwapper, AuthorizeSwapperAction, updateAtomConnectionStatus, UpdateAtomConnectionStatusAction } from "../actions/settlement/atomicActions";
import { ApplicationData } from "../reducers/types";

interface StoreProps {
    sdk: RenExSDK;
    atomConnectionStatus: AtomicConnectionStatus;
}

// // tslint:disable-next-line:no-any
// type ConnectedAction<F extends (args: any) => (dispatch: Dispatch) => Promise<any>> =
//     F extends (...args: infer Args) => (d: Dispatch) => Promise<infer X> ? (...args: Args) => Promise<X> : never;

interface AtomicNoticeProps extends StoreProps {
    actions: {
        authorizeSwapper: AuthorizeSwapperAction;
        updateAtomConnectionStatus: UpdateAtomConnectionStatusAction;
    };
}

interface AtomicNoticeState {
    linkError: string | null;
    linkPending: boolean;
}

class InnerAtomicNotice extends React.Component<AtomicNoticeProps, AtomicNoticeState> {

    constructor(props: AtomicNoticeProps) {
        super(props);
        this.state = {
            linkError: null,
            linkPending: false,
        };
    }

    // tslint:disable:jsx-no-lambda
    public render(): JSX.Element {
        const { sdk, atomConnectionStatus } = this.props;
        const { linkPending, linkError } = this.state;

        const reconnect = <button onClick={async () => { await this.handleLinkAtom(sdk.atom.resetStatus); }} disabled={linkPending}>
            {linkPending ? <Loading /> : "Retry"}
        </button>;

        let inner;

        switch (atomConnectionStatus) {
            case AtomicConnectionStatus.NotConnected:
                inner = <>
                    <h2>Could not detect Swapperd</h2>
                    <p>The Swapperd software is required for trading atomic swap pairs and viewing atomic balances. You can download the software using the links below:</p>
                    <ul>
                        <li><a href="https://releases.republicprotocol.com/swapperd/Swapperd.exe">Swapperd for Windows</a></li>
                        <li><a href="https://releases.republicprotocol.com/swapperd/Swapperd.dmg">Swapperd for MacOS</a></li>
                    </ul>
                    {linkError && <p className="red">{linkError}</p>}
                    {reconnect}
                </>;
                break;
            case AtomicConnectionStatus.NotAuthorized:
                inner = <>
                    <p>Your Swapperd has not been configured for this wallet. Please update your Swapperd configuration.</p>
                    {linkError && <p className="red">{linkError}</p>}
                    {reconnect}
                </>;
                break;
            case AtomicConnectionStatus.AtomNotAuthorized:
                inner = <>
                    <p>Your Swapperd needs to be authorized to action orders on your behalf.</p>
                    {linkError && <p className="red">{linkError}</p>}
                    { /* tslint:disable-next-line:no-any */}
                    <button onClick={async () => { await this.handleLinkAtom(() => (this.props.actions.authorizeSwapper as any)(sdk)); }} disabled={linkPending}>
                        {linkPending ? <Loading /> : linkError ? "Retry" : "Authorize"}
                    </button>
                </>;
                break;
            case AtomicConnectionStatus.ChangedSwapper:
                inner = <>
                    <p>Your Swapperd details have changed. After ensuring your Swapperd details are correct and have been correctly configured correctly, click the Reconnect button.</p>
                    {linkError && <p className="red">{linkError}</p>}
                    {reconnect}
                </>;
                break;
            case AtomicConnectionStatus.InvalidSwapper:
                inner = <>
                    <p>Unable to verify authenticity of Swapperd. Please try updating the application.</p>
                    {linkError && <p className="red">{linkError}</p>}
                    {reconnect}
                </>;
                break;
            default:
                inner = <p>Unknown Swapperd connection status: {atomConnectionStatus}</p>;
        }

        return <div className="order--wrapper">
            {inner}
        </div>;
    }
    // tslint:enable:jsx-no-lambda

    private handleLinkAtom = async (atomAction: () => Promise<AtomicConnectionStatus>) => {
        const { actions } = this.props;
        this.setState({
            linkError: null,
            linkPending: true,
        });
        return atomAction().then(atomConnectionStatus => {
            this.setState({
                linkPending: false,
            });
            actions.updateAtomConnectionStatus({ atomConnectionStatus });
        }).catch(() => {
            this.setState({
                linkPending: false,
            });
        });
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        sdk: state.sdk.sdk,
        atomConnectionStatus: state.trader.atomConnectionStatus,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: AtomicNoticeProps["actions"] } {
    return {
        actions: bindActionCreators({
            authorizeSwapper,
            updateAtomConnectionStatus,
        }, dispatch)
    };
}

export const AtomicNotice = connect(mapStateToProps, mapDispatchToProps)(InnerAtomicNotice);
