import BigNumber from "bignumber.js";

import * as React from "react";

import RenExSDK, { Token, TokenCode, TraderOrder } from "@renex/renex";
import { List, OrderedMap } from "immutable";
import { connect } from "react-redux";

import Heading from "./Heading";

import { RenExTokens, TokenDetails, UnknownToken } from "../lib/market";
import { networkData } from "../lib/network";
import { ApplicationData, LabelType } from "../reducers/types";
import { InfoLabel } from "./InfoLabel";

interface StoreProps {
    sdk: RenExSDK;
    supportedTokens: List<TokenCode>;
    accountBalances: OrderedMap<TokenCode, BigNumber | null>;
    balances: OrderedMap<TokenCode, BigNumber | null>;
    usableBalances: OrderedMap<TokenCode, BigNumber | null>;
    traderOrders: OrderedMap<string, TraderOrder>;
}

interface ViewBalancesProps extends StoreProps {
}

interface ViewBalancesState {
}

/**
 * ViewBalances is a visual component for displaying the user's Ether and token
 * balances
 */
class ViewBalances extends React.Component<ViewBalancesProps, ViewBalancesState> {

    public render(): JSX.Element {
        return (
            <div className="section view">
                <div className="container">
                    <Heading title="Your balances" />
                    <div className="table--wrapper">
                        <table>
                            <thead>
                                <tr>
                                    <th>Token</th>
                                    <th>
                                        Non-Deposited Balance
                                </th>
                                    <th>
                                        Deposited Balance
                                    <InfoLabel>The amount you have deposited to your RenEx wallet.</InfoLabel>
                                    </th>
                                    <th>
                                        Usable Balance
                                    <InfoLabel>The amount you have deposited, excluding balances locked in open orders or pending withdrawals.</InfoLabel>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.generateRows()}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }

    private generateRows(): JSX.Element[] {
        const { balances, usableBalances, accountBalances, supportedTokens } = this.props;

        const list: JSX.Element[] = [];
        RenExTokens(supportedTokens).forEach((token: TokenCode) => {
            const { name, symbol, icon, address } = TokenDetails.get(token, UnknownToken);
            const balance = balances.get(token, null);
            const usableBalance = usableBalances.get(token, null);
            const accountBalance = accountBalances.get(token, null);

            const readableBalanceText = (balance !== null) ? balance.toFixed() : "-";
            const readableUsableBalanceText = (usableBalance !== null) ? usableBalance.toFixed() : "-";
            const readableAccountBalanceText = (accountBalance !== null) ? accountBalance.toFixed() : "-";
            const image = require(`../tokens/${icon}`);

            let link;
            if (token !== Token.ETH) {
                const TOKEN_URL = `${networkData.etherscan}/token/${address}`;
                link =
                    <a className="view--external" href={TOKEN_URL} rel="noopener noreferrer" target="_blank" title="View on Etherscan">
                        <span>&rarr;</span>
                    </a>;
            }
            list.push(
                <tr key={token}>
                    <td>
                        <img alt="" role="presentation" src={image} />
                        <span>{`${name} `}</span>
                        {link}
                    </td>
                    <td>
                        {`${readableAccountBalanceText} ${symbol}`}
                        {accountBalance === null &&
                            <InfoLabel type={LabelType.Warning}>Failed to retrieve non-deposited {symbol} balance.</InfoLabel>
                        }
                    </td>
                    <td>
                        {`${readableBalanceText} ${symbol}`}
                        {balance === null &&
                            <InfoLabel type={LabelType.Warning}>Failed to retrieve deposited {symbol} balance.</InfoLabel>
                        }
                    </td>
                    <td>
                        {`${readableUsableBalanceText} ${symbol}`}
                        {usableBalance === null &&
                            <InfoLabel type={LabelType.Warning}>Failed to retrieve usable {symbol} balance.</InfoLabel>
                        }
                    </td>
                </tr >
            );
        });
        return list;
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        sdk: state.sdk.sdk,
        supportedTokens: state.sdk.supportedTokens,
        accountBalances: state.trader.accountBalances,
        balances: state.trader.balances,
        usableBalances: state.trader.usableBalances,
        traderOrders: state.traderBook.traderOrders,
    };
}

export default connect(mapStateToProps)(ViewBalances);
