import React from "react";

import BigNumber from "bignumber.js";

import { Formik, FormikBag, FormikErrors, FormikProps } from "formik";
import { OrderedMap } from "immutable";
import { connect } from "react-redux";
import { bindActionCreators, Dispatch } from "redux";

import RenExSDK, { OrderSettlement, OrderSide, OrderType, Token, TokenCode } from "@renex/renex";

import NewOrderInputForm from "./NewOrderInputForm";

import { openOrder, OpenOrderAction } from "../actions/trader/orderActions";
import { Pair, Pairs, TokenDetails, UnknownToken } from "../lib/market";
import { ApplicationData, MarketPrice, OrderInputs } from "../reducers/types";
import { InfoLabel } from "./InfoLabel";

interface StoreProps {
    sdk: RenExSDK;
    orderInputs: OrderInputs;
    minEthereumTradeVolume: BigNumber;
    usableBalances: OrderedMap<TokenCode, BigNumber | null>;
    usableAtomicBalances: OrderedMap<TokenCode, BigNumber | null>;
}

interface NewOrderInputsProps extends StoreProps {
    market: Pair;
    marketPrice: number;
    settlementType: OrderSettlement;
    actions: {
        openOrder: OpenOrderAction;
    };
}

// Shape of form values
export interface NewOrderInputsValues {
    volume: string;
    minVolume: string;
    priorityVolume: string;
    pricepoint: string;
}
// tslint:disable:jsx-no-lambda

interface NewOrderInputsState {
    side: OrderSide;
    showMoreOptions: boolean;
    allOrNothing: boolean;
    immediateOrCancel: boolean;
    fillOrKill: boolean;
}
class OuterForm extends React.Component<NewOrderInputsProps, NewOrderInputsState> {
    public constructor(props: NewOrderInputsProps) {
        super(props);
        this.state = {
            showMoreOptions: true,
            allOrNothing: false,
            immediateOrCancel: false,
            fillOrKill: false,
            side: OrderSide.BUY,
        };
    }

    public componentDidMount(): void {
        // Set side based on what user last saved
        const { orderInputs } = this.props;
        if (orderInputs.side !== this.state.side) {
            this.setState({ side: orderInputs.side });
        }
    }

    public render(): JSX.Element {
        const { fillOrKill, allOrNothing, immediateOrCancel, showMoreOptions } = this.state;
        const { sdk, orderInputs, market, marketPrice, usableBalances, usableAtomicBalances, settlementType } = this.props;
        const pairDetails = Pairs.get(market, undefined);
        if (!pairDetails) {
            return <></>;
        }

        const nonPriorityTokenDetails = TokenDetails.get(pairDetails.baseToken, UnknownToken);
        const { side } = this.state;
        const defaultMarketPrice = marketPrice ? new BigNumber(sdk.utils.normalizePrice(marketPrice)).toFixed() : "0";
        return (
            <>
                <div className="order--wrapper order--wrapper--tabbed">
                    <ul className="order--tabs">
                        <li className={side === OrderSide.BUY ? "active" : ""}
                            role="tab"
                            onClick={() => { this.setState({ side: OrderSide.BUY }); }}
                        >
                            <span>Buy {nonPriorityTokenDetails.symbol}</span>
                        </li>
                        <li className={side === OrderSide.SELL ? "active" : ""}
                            role="tab"
                            onClick={() => { this.setState({ side: OrderSide.SELL }); }}
                        >
                            <span>Sell {nonPriorityTokenDetails.symbol}</span>
                        </li>
                    </ul>
                    <Formik
                        initialValues={{
                            volume: orderInputs.volume || "",
                            minVolume: orderInputs.minVolume || "",
                            pricepoint: orderInputs.pricepoint || defaultMarketPrice,
                            priorityVolume: "",
                        }}
                        validate={this.validate}
                        onSubmit={this.handleSubmit}
                        render={(props: FormikProps<NewOrderInputsValues>) => (
                            <NewOrderInputForm
                                {...props}
                                market={market}
                                side={side}
                                allOrNothing={allOrNothing || fillOrKill}
                                initialMarketPrice={defaultMarketPrice}
                                settlementType={settlementType}
                                calculateAbsoluteMinTradeVolume={this.calculateAbsoluteMinTradeVolume}
                            />
                        )}
                    />
                </div>
                <div className={`order--options ${!showMoreOptions ? "hidden" : ""}`}>
                    <label>
                        <input
                            name="allOrNothing"
                            type="checkbox"
                            disabled={fillOrKill}
                            checked={allOrNothing || fillOrKill}
                            onChange={this.handleMoreOptions}
                        />
                        All or Nothing <InfoLabel>Execute either the entire order or not at all.</InfoLabel>
                    </label>
                    <label>
                        <input
                            name="immediateOrCancel"
                            type="checkbox"
                            disabled={fillOrKill}
                            checked={immediateOrCancel || fillOrKill}
                            onChange={this.handleMoreOptions}
                        />
                        Immediate or Cancel <InfoLabel>If the order cannot be matched against the current orderbook, cancel the order.</InfoLabel>
                    </label>
                    <label>
                        <input
                            name="fillOrKill"
                            type="checkbox"
                            disabled={allOrNothing && immediateOrCancel}
                            checked={fillOrKill || (allOrNothing && immediateOrCancel)}
                            onChange={this.handleMoreOptions}
                        />
                        Fill or Kill <InfoLabel>If the entire order cannot be executed against the current orderbook, cancel the order.</InfoLabel>
                    </label>
                </div>
            </>
        );
    }

    // tslint:disable-next-line:no-any
    private handleSubmit = async (values: NewOrderInputsValues, formikBag: FormikBag<NewOrderInputsProps, any>) => {
        const { setSubmitting, resetForm } = formikBag;
        const { market, sdk } = this.props;
        const { side, immediateOrCancel, fillOrKill } = this.state;
        const { pricepoint, volume, minVolume } = values;

        const orderType = immediateOrCancel || fillOrKill ? OrderType.LIMIT_IOC : OrderType.LIMIT;

        // Callback functions
        const onSuccess = () => {
            setSubmitting(false);
            resetForm({
                pricepoint,
                volume: "0",
                minVolume: "0",
                priorityVolume: "0",
            });
        };

        const onError = () => {
            setSubmitting(false);
        };

        this.props.actions.openOrder(
            sdk,
            market,
            side, pricepoint || "0",
            volume || "0", minVolume || "0",
            orderType,
            onSuccess,
            onError,
        );
    }

    private validate = (values: NewOrderInputsValues): FormikErrors<NewOrderInputsValues> => {
        const { sdk, minEthereumTradeVolume, market, settlementType } = this.props;
        const usableBalances = settlementType === OrderSettlement.RenEx ? this.props.usableBalances : this.props.usableAtomicBalances;
        const { side, allOrNothing } = this.state;

        const errors: FormikErrors<NewOrderInputsValues> = {};
        const marketDetails = Pairs.get(market, undefined);
        if (!marketDetails) {
            errors.volume = `Unknown market pair.`;
            return errors;
        }

        const sellToken = (side === OrderSide.BUY) ? marketDetails.quoteToken : marketDetails.baseToken;
        const whichVolume = (side === OrderSide.BUY) ? "priorityVolume" : "volume";
        const sellBalance = usableBalances.get(sellToken) || new BigNumber(0);
        const sellVolume = new BigNumber(values[whichVolume]);
        const sellTokenDetails = TokenDetails.get(sellToken, UnknownToken);

        const pricepointBigNumber = new BigNumber(values.pricepoint || "0");
        const volumeBigNumber = new BigNumber(values.volume || "0");
        const minVolumeBigNumber = new BigNumber(values.minVolume || "0");
        const priorityVolumeBigNumber = new BigNumber(values.priorityVolume || "0");

        // Ensure user has sufficient balance
        if (sellBalance.lt(sellVolume)) {
            errors[whichVolume] = `Insufficient ${sellTokenDetails.symbol} balance.`;
        }

        // Enforce minimum volume
        if (marketDetails.quoteToken === Token.ETH && priorityVolumeBigNumber.lt(minEthereumTradeVolume)) {
            errors.priorityVolume = `Minimum order size is ${minEthereumTradeVolume.toFixed()} ETH.`;
        }

        // Ensure pricepoint is non zero before enforcing absolute minimum volume
        if (!pricepointBigNumber.isZero()) {
            const absoluteMinVolume = this.calculateAbsoluteMinTradeVolume(marketDetails.baseToken, pricepointBigNumber);
            const nonPriorityDetails = TokenDetails.get(marketDetails.baseToken, UnknownToken);
            if (minVolumeBigNumber.lt(absoluteMinVolume)) {
                const whichMinVolume = allOrNothing ? "volume" : "minVolume";
                const volumeString = allOrNothing ? "Volume" : "Minimum volume";
                errors[whichMinVolume] = `${volumeString} must be at least ${absoluteMinVolume.toFixed()} ${nonPriorityDetails.symbol}.`;
            }
        }

        // Minimum volume should be less than volume
        if (volumeBigNumber.lt(minVolumeBigNumber)) {
            errors.volume = `Volume cannot be less than minimum volume.`;
        }

        return errors;
    }

    private handleMoreOptions = (e: React.ChangeEvent<HTMLInputElement>): void => {
        const { name, checked } = e.target;
        this.setState((oldState) => ({ ...oldState, [name]: checked, }));
    }

    private calculateAbsoluteMinTradeVolume = (baseToken: string, price: BigNumber): BigNumber => {
        const { sdk, minEthereumTradeVolume } = this.props;
        switch (baseToken) {
            case Token.ETH:
                return minEthereumTradeVolume;
            case Token.WBTC:
                return new BigNumber(0.0002);
            default:
                return new BigNumber(sdk.utils.normalizeVolume(minEthereumTradeVolume.dividedBy(price), true));
        }
    }

}

// tslint:enable:jsx-no-lambda

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        sdk: state.sdk.sdk,
        orderInputs: state.inputs.orderInputs,
        minEthereumTradeVolume: state.sdk.minEthTradeVolume,
        usableBalances: state.trader.usableBalances,
        usableAtomicBalances: state.trader.usableAtomicBalances,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: NewOrderInputsProps["actions"] } {
    return {
        actions: bindActionCreators({
            openOrder,
        }, dispatch)
    };
}

export const NewOrderInputs = connect(mapStateToProps, mapDispatchToProps)(OuterForm);
