import { createStandardAction } from "typesafe-actions";

import { Pair } from "../../lib/market";
import { BalanceInputs, OrderInputs } from "../../reducers/types";

/**
 * Updates the current market (token pair, e.g. ETH/REN)
 * @param  {{ market: Pair }} payload - the market being set
 */
interface SetMarketPayload { market: Pair; }
export type SetMarketAction = (payload: SetMarketPayload) => void;
export const setMarket = createStandardAction("SET_MARKET")<SetMarketPayload>();

/**
 * Updates the NewOrder inputs
 * @param  {{ orderInputs: OrderInputs }} payload
 */
interface SetOrderPayload { orderInputs: OrderInputs; }
export type SetOrderAction = (payload: SetOrderPayload) => void;
export const setOrder = createStandardAction("SET_ORDER")<SetOrderPayload>();

/**
 * Updates the ManageBalance inputs
 * @param  {{ orderInputs: OrderInputs }} payload
 */
interface SetBalancePayload { balanceInputs: BalanceInputs; }
export type SetBalanceAction = (payload: SetBalancePayload) => void;
export const setBalance = createStandardAction("SET_BALANCE")<SetBalancePayload>();
