import * as React from "react";

import { OrderedMap } from "immutable";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { Dispatch } from "redux";
import { bindActionCreators } from "redux";

import { setMarket, SetMarketAction } from "../actions/inputs/inputsActions";
import { Pair, PairDetails, Pairs, TokenDetails } from "../lib/market";
import { ApplicationData, MarketPrice } from "../reducers/types";

interface StoreProps {
    market: Pair;
    marketPrices: OrderedMap<Pair, MarketPrice>;
}

interface MarketBannerProps extends StoreProps {
    actions: {
        setMarket: SetMarketAction;
    };
}

interface MarketBannerState {
}

/**
 * MarketBanner is a visual component for displaying market price changes in a
 * banner format
 */
class MarketBanner extends React.Component<MarketBannerProps, MarketBannerState> {
    public render(): JSX.Element {
        return (
            <div className="market">
                <div className="container">
                    <ul className="market--items">
                        {this.listPairs()}
                    </ul>
                </div>
            </div>
        );
    }

    private listPairs(): JSX.Element[] {
        const { marketPrices } = this.props;
        let key = 0;
        return Pairs.map((pairDetails: PairDetails) => {
            key++;
            const pair = pairDetails.code;
            const marketPrice = marketPrices.get(pair, undefined);
            if (marketPrice === undefined || pair === Pair.BTC_WBTC) {
                return <div key={key} />;
            }
            const percentChange = marketPrice ? `${marketPrice.percentChange.toFixed(2)}%` : "";
            const price = marketPrice ? marketPrice.price : "";
            const fstDetails = TokenDetails.get(pairDetails.quoteToken, undefined);
            const sndDetails = TokenDetails.get(pairDetails.baseToken, undefined);

            if (!fstDetails || !sndDetails) {
                return <div key={key} />;
            }

            return (
                <li key={key} title={price.toString()}>
                    <Link to="/exchange" onClick={this.updateMarket.bind(this, pair)}>
                        <span>{`${sndDetails.symbol}/${fstDetails.symbol}`}:</span>
                    </Link>
                    <span className={`${marketPrice.percentChange < 0 ? "negative" : "positive"}`}>{percentChange}</span>
                </li>
            );
        }).toArray();
    }

    private updateMarket(market: Pair): void {
        if (market !== this.props.market) {
            this.props.actions.setMarket({ market });
        }
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        market: state.inputs.market,
        marketPrices: state.marketPrices.marketPrices,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: MarketBannerProps["actions"] } {
    return {
        actions: bindActionCreators({
            setMarket,
        }, dispatch)
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(MarketBanner);
