import * as React from "react";

import RenExSDK, { AtomicConnectionStatus } from "@renex/renex";

import { Dispatch } from "redux";

import SigningPopup from "../../components/popups/SigningPopup";

import { createStandardAction } from "typesafe-actions";
import { clearPopup, setPopup } from "../../actions/popup/popupActions";

interface UpdateAtomConnectionStatusPayload { atomConnectionStatus: AtomicConnectionStatus; }
export type UpdateAtomConnectionStatusAction = (payload: UpdateAtomConnectionStatusPayload) => void;
export const updateAtomConnectionStatus = createStandardAction("UPDATE_ATOM_CONNECTION_STATUS")<UpdateAtomConnectionStatusPayload>();

export type RefreshAtomConnectionStatusAction = (sdk: RenExSDK) => (dispatch: Dispatch) => Promise<void>;
export const refreshAtomConnectionStatus: RefreshAtomConnectionStatusAction = (sdk) => async (dispatch) => {
    const atomConnectionStatus = await sdk.atom.refreshStatus();
    dispatch(updateAtomConnectionStatus({ atomConnectionStatus }));
};

export type AuthorizeSwapperAction = (sdk: RenExSDK) => (dispatch: Dispatch) => Promise<void>;
export const authorizeSwapper: AuthorizeSwapperAction = (sdk) => async (dispatch) => new Promise((resolve, reject) => {

    const data: string[] = [`RenEx:`, `authorize:`, sdk._atomConnectedAddress];

    const call = async () => {
        const status = await sdk.atom.authorize();
        dispatch(clearPopup());
        return resolve(status);
    };

    const onCancel = () => {
        dispatch(clearPopup());
        reject();
    };

    const popup = <SigningPopup data={data} sign={call} />;

    dispatch(setPopup({ popup, onCancel }));
    // tslint:disable-next-line:no-any
}) as any;
