import BigNumber from "bignumber.js";

import * as Sentry from "@sentry/browser";

import RenExSDK, { BalanceActionType, TokenCode, TransactionStatus } from "@renex/renex";

import { OrderedMap } from "immutable";
import { Dispatch } from "redux";
import { createStandardAction } from "typesafe-actions";

import { second, sleep } from "../../lib/conversion";
import { TokenDetails } from "../../lib/market";
import { Alert, AlertData, AlertType } from "../../reducers/types";
import { BalanceActionRecord } from "../../reducers/typesSdk";
import { setAlert } from "../alert/alertActions";
import { addPendingAlert, removePendingAlerts } from "../alert/pendingAlertActions";
import { updateAccountBalances, updateAtomicAddresses, updateAtomicBalances, updateBalances, updateUsableAtomicBalances, updateUsableBalances } from "../trader/balanceActions";

/**
 * Updates a withdrawal or deposit in the store (stored based on its tx hash)
 * @param {{ balanceItem: BalanceAction }} payload - The payload should contain
 * the withdrawal or deposit object
 */
type StoreBalanceActionsPayload = BalanceActionRecord[];
export type StoreBalanceActionsAction = (payload: StoreBalanceActionsPayload) => void;
export const storeBalanceActions = createStandardAction("PUSH_BALANCE")<StoreBalanceActionsPayload>();

/**
 * Checks if any pending deposits or withdrawals have been confirmed
 * @param web3 - Web3 instance to use
 * @param balanceHistory - The Balance History state
 */
export type UpdateBalancesHistoryAction = (sdk: RenExSDK, balanceHistory: OrderedMap<string, BalanceActionRecord>, checkAll?: boolean)
    => (dispatch: Dispatch) => Promise<void>;
export const updateBalancesHistory: UpdateBalancesHistoryAction = (sdk, balanceHistory, checkAll = false) => async (dispatch) => {

    // If `checkAll` is true, check all balance history items
    let filter = (balanceAction: BalanceActionRecord | undefined) => balanceAction !== undefined && balanceAction.status === TransactionStatus.Pending;
    if (checkAll) {
        filter = () => true;
    }

    balanceHistory
        .filter(filter).toArray()
        .map(async (balanceAction: BalanceActionRecord) => {
            try {
                let balanceActionStatus = await sdk.fetchBalanceActionStatus(balanceAction.txHash);

                // If the transaction has been replaced, check again after a
                // delay. This should not be a common occurrence.
                if (balanceActionStatus === TransactionStatus.Replaced) {
                    await sleep(5 * second);
                    balanceActionStatus = await sdk.fetchBalanceActionStatus(balanceAction.txHash);
                }

                if (balanceActionStatus !== balanceAction.status) {
                    if (balanceActionStatus === TransactionStatus.Done) {
                        const alert = async () => {
                            const tokenDetails = TokenDetails.get(balanceAction.token);
                            dispatch(setAlert({
                                alert: new Alert({
                                    message: `${balanceAction.amount} ${tokenDetails.symbol} has been successfully ${balanceAction.action === BalanceActionType.Deposit ? "deposited" : "withdrawn"}`,
                                    alertType: AlertType.Success,
                                }),
                            }));
                        };
                        dispatch(addPendingAlert({ id: balanceAction.txHash, method: alert }));
                    }
                    balanceAction = balanceAction.set("status", balanceActionStatus);
                    dispatch(storeBalanceActions([balanceAction]));
                }
            } catch (err) {
                console.error(err);
                Sentry.captureException(err);
            }
        });
};

export type GetBalanceAction = (sdk: RenExSDK, pendingAlerts?: AlertData["pendingAlerts"]) => (dispatch: Dispatch) => Promise<void>;
export const getBalances: GetBalanceAction = (sdk, pendingAlerts) => async (dispatch) => {
    // getBalances should not throw any errors

    /* Token balances */
    try {
        let usableBalances = OrderedMap<TokenCode, BigNumber | null>();
        let balances = OrderedMap<TokenCode, BigNumber | null>();
        let accountBalances = OrderedMap<TokenCode, BigNumber | null>();

        const supportedTokens = await sdk.fetchSupportedTokens();
        const allTokenBalances = await sdk.fetchBalances(supportedTokens);
        supportedTokens.forEach((token: TokenCode) => {
            let free = null;
            let total = null;
            let nondeposited = null;
            const tokenBalance = allTokenBalances.get(token);
            if (tokenBalance) {
                free = tokenBalance.free;
                if (tokenBalance.free !== null && tokenBalance.used !== null) {
                    total = tokenBalance.free.plus(tokenBalance.used);
                }
                nondeposited = tokenBalance.nondeposited;
            }
            usableBalances = usableBalances.set(token, free);
            balances = balances.set(token, total);
            accountBalances = accountBalances.set(token, nondeposited);
        });
        dispatch(updateUsableBalances({ usableBalances }));
        dispatch(updateAccountBalances({ accountBalances }));
        dispatch(updateBalances({ balances }));
    } catch (err) {
        console.error(err);
        Sentry.captureException(err);
    }

    // If there are pending alerts (e.g. Orders settled, withdrawals confirmed)
    // call them now and clear them
    if (pendingAlerts) {
        dispatch(removePendingAlerts({ ids: pendingAlerts.keySeq().toArray() }));
        pendingAlerts.toArray().map((pendingAlert: () => Promise<void>) => {
            pendingAlert().catch(console.error);
        });
    }


};

export type GetAtomicBalanceAction = (sdk: RenExSDK) => (dispatch: Dispatch) => Promise<void>;
export const getAtomicBalances: GetAtomicBalanceAction = (sdk) => async (dispatch) => {
    if (!sdk.atom.isConnected()) {
        console.error("Cannot get atomic balances. Swapperd is not connected.");
        return;
    }
    try {
        const supportedTokens = await sdk.fetchSupportedAtomicTokens();
        await Promise.all([
            // Update the addresses
            sdk.atom.fetchAddresses(supportedTokens).then(atomicTokenAddresses => {
                let atomicAddresses = OrderedMap<TokenCode, string>();
                supportedTokens.map((token, index) => {
                    atomicAddresses = atomicAddresses.set(token, atomicTokenAddresses[index]);
                });
                dispatch(updateAtomicAddresses({ atomicAddresses }));
            }).catch(console.error),
            // Update the atomic balances
            sdk.atom.fetchBalances(supportedTokens).then(tokenBalancesMap => {
                let atomicBalances = OrderedMap<TokenCode, BigNumber | null>();
                let usableAtomicBalances = OrderedMap<TokenCode, BigNumber | null>();
                supportedTokens.forEach(token => {
                    const tokenBalance = tokenBalancesMap.get(token);
                    if (tokenBalance) {
                        let totalBalance: BigNumber | null = null;
                        if (tokenBalance.free !== null && tokenBalance.used !== null) {
                            totalBalance = tokenBalance.free.plus(tokenBalance.used);
                        }
                        atomicBalances = atomicBalances.set(token, totalBalance);
                        usableAtomicBalances = usableAtomicBalances.set(token, tokenBalance.free);
                    }
                });
                dispatch(updateAtomicBalances({ atomicBalances }));
                dispatch(updateUsableAtomicBalances({ usableAtomicBalances }));
            }).catch(console.error),
        ]);
    }
    catch (err) {
        console.error(err);
        Sentry.captureException(err);
    }
};
