import BigNumber from "bignumber.js";

import * as React from "react";

import Select, { Option } from "react-select";

import { List, OrderedMap } from "immutable";
import { connect } from "react-redux";
import { Dispatch } from "redux";
import { bindActionCreators } from "redux";

import RenExSDK, { BalanceAction, BalanceActionType, Token, TokenCode, TraderOrder, TransactionStatus } from "@renex/renex";

import Heading from "./Heading";

import { setAlert, SetAlertAction } from "../actions/alert/alertActions";
import { setBalance, SetBalanceAction } from "../actions/inputs/inputsActions";
import { deposit, DepositAction, ETH_REQUIRED, withdraw, WithdrawAction } from "../actions/trader/balanceActions";
import { RenExTokens, TokenDetails, UnknownToken } from "../lib/market";
import { ErrorCanceledByUser } from "../lib/wallets/wallet";
import { Alert, AlertType, ApplicationData, BalanceInputs, Settlements } from "../reducers/types";
import { CustomOption, CustomValue } from "./SelectMarket";

interface StoreProps {
    accountBalances: OrderedMap<TokenCode, BigNumber | null>;
    balanceHistory: OrderedMap<string, BalanceAction>;
    balanceInputs: BalanceInputs;
    balances: OrderedMap<TokenCode, BigNumber | null>;
    traderOrders: OrderedMap<string, TraderOrder>;
    usableBalances: OrderedMap<TokenCode, BigNumber | null>;
    sdk: RenExSDK;
    supportedTokens: List<TokenCode>;
}

interface ManageBalancesProps extends StoreProps {
    actions: {
        setAlert: SetAlertAction;
        deposit: DepositAction;
        withdraw: WithdrawAction;
        setBalance: SetBalanceAction;
    };
}

interface ManageBalancesState {
    currency: TokenCode;
    depositAmount: string;
    withdrawAmount: string;
    balanceHistorySize: number;
    pendingWithdrawal: boolean;
}

/**
 * ManageBalances is a visual component for allowing a user to deposit and
 * withdraw funds
 */
class ManageBalances extends React.Component<ManageBalancesProps, ManageBalancesState> {
    public constructor(props: ManageBalancesProps, context: object) {
        super(props, context);
        const { currency, depositAmount, withdrawAmount } = this.props.balanceInputs;
        this.state = {
            currency,
            depositAmount,
            withdrawAmount,
            balanceHistorySize: 0,
            pendingWithdrawal: false,
        };
        this.deposit = this.deposit.bind(this);
        this.withdraw = this.withdraw.bind(this);
        this.fillDeposit = this.fillDeposit.bind(this);
        this.fillWithdraw = this.fillWithdraw.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleInput = this.handleInput.bind(this);
        this.handleBlur = this.handleBlur.bind(this);
    }

    public componentWillReceiveProps(nextProps: ManageBalancesProps): void {
        const { balanceHistory, balanceInputs } = nextProps;
        const { currency } = balanceInputs;
        if (currency !== this.state.currency) {
            this.setState({ currency });
        }

        // Check for pending withdrawal updates if there are new items in the
        // history or if there is an existing pending withdrawal.
        if (balanceHistory.size !== this.state.balanceHistorySize || this.state.pendingWithdrawal) {
            this.setState({ balanceHistorySize: balanceHistory.size });
            const pendingWithdrawal = balanceHistory.filter((item: BalanceAction | undefined) => {
                return item !== undefined && item.action === BalanceActionType.Withdraw && item.status === TransactionStatus.Pending;
            }).size > 0;
            if (pendingWithdrawal !== this.state.pendingWithdrawal) {
                this.setState({ pendingWithdrawal });
            }
        }
    }

    public componentWillUnmount() {
        const { currency, depositAmount, withdrawAmount } = this.state;
        if (!currency || !depositAmount || !withdrawAmount) {
            return;
        }
        const balanceInputs: BalanceInputs = { currency, depositAmount, withdrawAmount };
        this.props.actions.setBalance({ balanceInputs });
    }

    public render(): JSX.Element {
        const { usableBalances, accountBalances } = this.props;
        const { currency, depositAmount, withdrawAmount } = this.state;

        const parsedDepositAmount = new BigNumber(depositAmount);
        const parsedWithdrawAmount = new BigNumber(withdrawAmount);

        const depositable = accountBalances.get(currency) || new BigNumber(0);
        const withdrawable = usableBalances.get(currency) || new BigNumber(0);

        const disableDeposit =
            !parsedDepositAmount.gt(new BigNumber(0)) ||
            !parsedDepositAmount.lte(depositable);
        const disableWithdraw =
            !parsedWithdrawAmount.gt(new BigNumber(0)) ||
            !parsedWithdrawAmount.lte(withdrawable);

        return (
            <div className="manage">
                <div className="container">
                    <Heading title="Deposit or Withdraw" />
                    <div className="manage--form">
                        <Select
                            className="Select--currency"
                            name="token"
                            value={currency}
                            onChange={this.handleChange}
                            options={this.listCurrencies()}
                            // tslint:disable-next-line:no-any
                            valueComponent={CustomValue as any}
                            optionComponent={CustomOption}
                            clearable={false}
                            backspaceRemoves={false}
                            deleteRemoves={false}
                        />
                        <div className="manage--row">
                            <label>
                                <div className="manage--title">
                                    <span>Deposit (<span
                                        role="button"
                                        className="manage--option"
                                        onClick={this.fillDeposit}
                                    >Max</span>)</span>
                                </div>
                                <span className="manage--input-item">
                                    <input
                                        role="textbox"
                                        type="number"
                                        value={depositAmount}
                                        name="depositAmount"
                                        min={0}
                                        onChange={this.handleInput}
                                        onBlur={this.handleBlur}
                                    />
                                    <span>{TokenDetails.get(currency, UnknownToken).symbol}</span>
                                    <button
                                        disabled={disableDeposit}
                                        onClick={this.deposit}
                                    >Deposit</button>
                                </span>
                            </label>
                            <label>
                                <div className="manage--title">
                                    <span>Withdraw (<span
                                        role="button"
                                        className="manage--option"
                                        onClick={this.fillWithdraw}
                                    >Max</span>)</span>
                                </div>
                                <span className="manage--input-item">
                                    <input
                                        role="textbox"
                                        type="number"
                                        value={withdrawAmount}
                                        name="withdrawAmount"
                                        min={0}
                                        onChange={this.handleInput}
                                        onBlur={this.handleBlur}
                                    />
                                    <span>{TokenDetails.get(currency, UnknownToken).symbol}</span>
                                    <button
                                        disabled={disableWithdraw}
                                        onClick={this.withdraw}
                                    >Withdraw</button>
                                </span>
                            </label>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    private async deposit(): Promise<void> {
        const { sdk, balanceHistory, traderOrders, usableBalances, accountBalances } = this.props;
        const { currency, depositAmount } = this.state;

        try {
            await this.props.actions.deposit(
                sdk, balanceHistory, traderOrders, usableBalances, accountBalances, depositAmount, currency,
            );
        } catch (err) {
            if (err.message !== ErrorCanceledByUser) {
                this.props.actions.setAlert({
                    alert: new Alert({
                        alertType: AlertType.Error,
                        message: err.message || err,
                    })
                });
            }
        }
    }

    private async withdraw(): Promise<void> {
        const { sdk, balances, usableBalances } = this.props;
        const { currency, withdrawAmount, pendingWithdrawal } = this.state;

        if (pendingWithdrawal) {
            this.props.actions.setAlert({ alert: new Alert({ message: "Please wait until the current withdrawal is complete" }) });
            return;
        }

        try {
            await this.props.actions.withdraw(
                sdk, balances, usableBalances, withdrawAmount, currency,
            );
        } catch (err) {
            if (err.message !== ErrorCanceledByUser) {
                this.props.actions.setAlert({
                    alert: new Alert({
                        alertType: AlertType.Error,
                        message: err.message || err,
                    })
                });
            }
        }
    }

    private async fillDeposit(): Promise<void> {
        const { sdk, accountBalances } = this.props;
        const { currency } = this.state;
        let depositAmount = accountBalances.get(currency) || new BigNumber(0);

        // Subtract necessary ETH from deposit amount to account for
        // deposit/withdrawal fees as well as additional fees for opening
        // orders.
        if (currency === Token.ETH) {
            depositAmount = depositAmount.minus(new BigNumber(ETH_REQUIRED));
            if (depositAmount.lt(new BigNumber(0))) {
                depositAmount = new BigNumber(0);
            }
        }

        this.setState({ depositAmount: depositAmount.toFixed() });
    }

    private fillWithdraw(): void {
        const { usableBalances } = this.props;
        const { currency } = this.state;
        const withdrawAmount = usableBalances.get(currency) || new BigNumber(0);
        this.setState({ withdrawAmount: withdrawAmount.toFixed() });
    }

    private listCurrencies(): Option[] {
        const { supportedTokens } = this.props;
        const list: Option[] = [];
        RenExTokens(supportedTokens).forEach((token: TokenCode) => {
            const { symbol, name } = TokenDetails.get(token, UnknownToken);
            list.push({
                label: `${symbol}  ${name}`,
                name,
                symbol,
                value: token,
            });
        });
        return list;
    }

    private handleChange(selected: { value: TokenCode; label: string }): void {
        const { depositAmount, withdrawAmount } = this.state;
        const currency = selected.value;
        // if (!currency || !depositAmount || !withdrawAmount) {
        //     return;
        // }
        this.setState({ currency });
        const balanceInputs: BalanceInputs = { currency, depositAmount, withdrawAmount };
        this.props.actions.setBalance({ balanceInputs });
    }

    private handleInput(event: React.FormEvent<HTMLInputElement>): void {
        const element = (event.target as HTMLInputElement);
        this.setState((current) => ({ ...current, [element.name]: element.value }));
    }

    private handleBlur(event: React.FormEvent<HTMLInputElement>): void {
        const { accountBalances, usableBalances } = this.props;
        const { currency } = this.state;
        const element = (event.target as HTMLInputElement);

        let maximum: BigNumber;
        switch (element.name) {
            case "withdrawAmount": maximum = usableBalances.get(currency) || new BigNumber(0); break;
            case "depositAmount": maximum = accountBalances.get(currency) || new BigNumber(0); break;
            default: return;
        }

        // Cap the input to available balance
        let input = new BigNumber(element.value);
        if (input.gt(maximum)) {
            input = maximum;
        }
        this.setState((current) => ({ ...current, [element.name]: input }));
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        accountBalances: state.trader.accountBalances,
        balanceHistory: state.balanceHistory.balanceHistory,
        balanceInputs: state.inputs.balanceInputs,
        balances: state.trader.balances,
        traderOrders: state.traderBook.traderOrders,
        usableBalances: state.trader.usableBalances,
        sdk: state.sdk.sdk,
        supportedTokens: state.sdk.supportedTokens,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: ManageBalancesProps["actions"] } {
    return {
        actions: bindActionCreators({
            deposit,
            setAlert,
            setBalance,
            withdraw,
        }, dispatch)
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(ManageBalances);
