import * as React from "react";

import createClass from "create-react-class";
import Select, { Option, OptionComponentProps, OptionValues } from "react-select";

import { OrderSettlement, TokenCode } from "@renex/renex";
import { List } from "immutable";
import { connect } from "react-redux";
import { bindActionCreators, Dispatch } from "redux";

import history from "../lib/history";

import { setMarket, SetMarketAction } from "../actions/inputs/inputsActions";
import { Pair, Pairs, TokenDetails, UnknownToken } from "../lib/market";
import { ApplicationData } from "../reducers/types";

interface StoreProps {
    market: Pair;
    supportedTokens: List<TokenCode>;
    supportedAtomicTokens: List<TokenCode>;
}

interface SelectMarketProps extends StoreProps {
    actions: {
        setMarket: SetMarketAction;
    };
}

interface SelectMarketState {
    fstCode: TokenCode | undefined;
    sndCode: TokenCode | undefined;
}

// This is for modifying the react-select options
// tslint:disable-next-line:no-any
export const CustomValue: React.SFC<OptionValues> = (props: any) => {
    const filename = TokenDetails.get(props.value.value, UnknownToken).icon;
    const image = require(`../tokens/${filename}`);
    return (
        <div className="Select-value">
            <span className="Select-value-label">
                <img alt="" role="presentation" src={image} />
                <span><b>{props.value.symbol}</b>  {props.value.name}{props.value.orderSettlement === OrderSettlement.RenExAtomic ? " (atomic)" : ""}</span>
            </span>
        </div>
    );
};

export const CustomOption: React.ClassicComponentClass<OptionComponentProps> = createClass({
    handleMouseDown(event: React.MouseEvent<HTMLElement>): void {
        event.preventDefault();
        event.stopPropagation();
        this.props.onSelect(this.props.option, event);
    },

    handleMouseEnter(event: React.MouseEvent<HTMLElement>): void {
        this.props.onFocus(this.props.option, event);
    },

    handleMouseMove(event: React.MouseEvent<HTMLElement>): void {
        if (this.props.isFocused) {
            return;
        }
        this.props.onFocus(this.props.option, event);
    },

    render(): JSX.Element {
        const filename = TokenDetails.get(this.props.option.value, UnknownToken).icon;
        const image = require(`../tokens/${filename}`);
        return (
            <div
                role="button"
                className={this.props.className}
                onMouseDown={this.handleMouseDown}
                onMouseEnter={this.handleMouseEnter}
                onMouseMove={this.handleMouseMove}
            >
                <img role="presentation" alt="" src={image} />
                {this.props.children}
            </div>
        );
    }
});

/**
 * SelectMarket is a visual component for displaying an arbitrary component in the
 * foreground with the rest of the page in the background
 */
class SelectMarket extends React.Component<SelectMarketProps, SelectMarketState> {
    public constructor(props: SelectMarketProps, context: object) {
        super(props, context);

        // Retrieve the order inputs from the store.
        const { market } = this.props;
        const marketDetails = Pairs.get(market, undefined);

        let fstCode;
        let sndCode;
        if (marketDetails) {
            fstCode = marketDetails.category || marketDetails.quoteToken;
            sndCode = fstCode === marketDetails.quoteToken ? marketDetails.baseToken : marketDetails.quoteToken;
        }
        this.state = {
            fstCode,
            sndCode,
        };

        this.handleChange = this.handleChange.bind(this);
    }

    public componentWillReceiveProps(nextProps: SelectMarketProps): void {
        const { market } = nextProps;
        const marketDetails = Pairs.get(market, undefined);

        if (marketDetails) {
            let { fstCode, sndCode } = this.state;
            if (market !== this.props.market) {
                fstCode = marketDetails.category || marketDetails.quoteToken;
                sndCode = fstCode === marketDetails.quoteToken ? marketDetails.baseToken : marketDetails.quoteToken;
                this.setState({ fstCode, sndCode });
            }
        }
    }

    public render(): JSX.Element {
        const { fstCode, sndCode } = this.state;

        if (fstCode === undefined || sndCode === undefined) {
            return <></>;
        }

        const leftCurrencies = this.listCurrencies();

        return (
            <div className="order--currencies">
                <Select
                    className="Select--currency"
                    name="fstCode"
                    value={fstCode}
                    onChange={this.handleChange}
                    options={leftCurrencies}
                    // tslint:disable-next-line:no-any
                    valueComponent={CustomValue as any}
                    optionComponent={CustomOption}
                    clearable={false}
                    backspaceRemoves={false}
                    deleteRemoves={false}
                    disabled={leftCurrencies.length < 2}
                />
                <Select
                    className="Select--currency"
                    name="sndCode"
                    value={sndCode}
                    onChange={this.handleChange}
                    options={this.listPairs(fstCode)}
                    // tslint:disable-next-line:no-any
                    valueComponent={CustomValue as any}
                    optionComponent={CustomOption}
                    clearable={false}
                    backspaceRemoves={false}
                    deleteRemoves={false}
                />
            </div>
        );
    }

    // private updateMarket(market: Pair): void {
    //     // let { fstCode, sndCode } = this.state;

    //     // // Check if the pair exists
    //     // if (!TokenDetails.get(fstCode).pairs.has(sndCode)) {
    //     //     // If not, check if the inverse of the pair exists
    //     //     if (TokenDetails.get(sndCode).pairs.has(fstCode)) {
    //     //         const fstCodeTmp = fstCode;
    //     //         fstCode = sndCode;
    //     //         sndCode = fstCodeTmp;
    //     //         this.setState({ fstCode, sndCode });
    //     //     } else {
    //     //         // Otherwise, check if there are any pairs in which it is the
    //     //         // first token and assign them. If it has no pairs, we revert
    //     //         // use Token.ETH.
    //     //         if (!TokenDetails.get(fstCode).pairs.size) {
    //     //             fstCode = Token.ETH;
    //     //             this.setState({ fstCode });
    //     //         }
    //     //         sndCode = Number(TokenDetails.get(fstCode).pairs.keySeq().first());
    //     //         if (!isNaN(sndCode)) {
    //     //             this.setState({ sndCode });
    //     //         }
    //     //     }
    //     // }

    //     // const market = TokenDetails.get(fstCode).pairs.get(sndCode);
    //     this.props.actions.setMarket({ market });
    // }

    // tslint:disable-next-line:no-any
    private handleChange(e: any): void {
        this.setState((current) => ({ ...current, [e.field]: e.value }), () => {
            // this.updateMarket(e.market);
            history.push("/exchange");
            this.props.actions.setMarket({ market: e.market });
        });
    }

    private listCurrencies(): Option[] {
        const { supportedTokens, supportedAtomicTokens } = this.props;
        const list: Option[] = [];
        const tokens = supportedTokens.concat(supportedAtomicTokens).toArray();
        Array.from(new Set(tokens)).forEach((token: TokenCode) => {
            // Check if token has any pairs in which it is the 'fstCode'.
            if (TokenDetails.get(token, UnknownToken).pairs.size > 0) {
                const { symbol, name, pairs } = TokenDetails.get(token, UnknownToken);
                list.push({
                    field: "fstCode",
                    label: `${symbol}  ${name}`,
                    name,
                    symbol,
                    value: token,
                    market: pairs.first(),
                });
            }
        });
        return list;
    }

    private listPairs(fstCode: TokenCode): Option[] {
        return TokenDetails.get(fstCode, UnknownToken).pairs.map((pair: Pair, token: TokenCode) => {
            const pairDetails = Pairs.get(pair, undefined);
            if (!pairDetails) {
                return;
            }
            const { orderSettlement, code } = pairDetails;
            const { symbol, name } = TokenDetails.get(token, UnknownToken);
            return {
                field: "sndCode",
                label: `${symbol}  ${name}${orderSettlement === OrderSettlement.RenExAtomic ? ` (atomic)` : ``}`,
                name,
                symbol,
                value: token,
                orderSettlement,
                market: code,
            };
        }).toArray().filter(x => x !== undefined) as Option[];
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        market: state.inputs.market,
        supportedTokens: state.sdk.supportedTokens,
        supportedAtomicTokens: state.sdk.supportedAtomicTokens,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: SelectMarketProps["actions"] } {
    return {
        actions: bindActionCreators(
            {
                setMarket,
            }, dispatch)
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(SelectMarket);
