export interface PairDetails {
    code: Pair;
    orderSettlement: OrderSettlement;
    quoteToken: TokenCode;
    baseToken: TokenCode;
    category?: TokenCode; // Force the pair to be shown under a specific token
    fixedPrice?: BigNumber;
}

export enum Pair {
    BTC_WBTC = 0x0000000000000002,
    BTC_ETH = 0x0000000000000001,
    ETH_DGX = 0x0000000100000100,
    ETH_TUSD = 0x0000000100000101,
    ETH_REN = 0x0000000100010000,
    ETH_ZRX = 0x0000000100010001,
    ETH_OMG = 0x0000000100010002,
}

export const Pairs: OrderedMap<Pair, PairDetails> = OrderedMap<Pair, PairDetails>()
    // RenExAtomic:
    .set(Pair.BTC_ETH, {
        code: Pair.BTC_ETH,
        orderSettlement: OrderSettlement.RenExAtomic,
        quoteToken: Token.BTC,
        baseToken: Token.ETH,
        category: Token.BTC,
    })
    .set(Pair.BTC_WBTC, {
        code: Pair.BTC_WBTC,
        orderSettlement: OrderSettlement.RenExAtomic,
        quoteToken: Token.BTC,
        baseToken: Token.WBTC,
        category: Token.BTC,
        fixedPrice: new BigNumber(1),
    })
    // RenEx:
    .set(Pair.ETH_DGX, {
        code: Pair.ETH_DGX,
        orderSettlement: OrderSettlement.RenEx,
        quoteToken: Token.ETH,
        baseToken: Token.DGX,
    })
    .set(Pair.ETH_TUSD, {
        code: Pair.ETH_TUSD,
        orderSettlement: OrderSettlement.RenEx,
        quoteToken: Token.ETH,
        baseToken: Token.TUSD,
    })
    .set(Pair.ETH_REN, {
        code: Pair.ETH_REN,
        orderSettlement: OrderSettlement.RenEx,
        quoteToken: Token.ETH,
        baseToken: Token.REN,
    })
    .set(Pair.ETH_ZRX, {
        code: Pair.ETH_ZRX,
        orderSettlement: OrderSettlement.RenEx,
        quoteToken: Token.ETH,
        baseToken: Token.ZRX,
    })
    .set(Pair.ETH_OMG, {
        code: Pair.ETH_OMG,
        orderSettlement: OrderSettlement.RenEx,
        quoteToken: Token.ETH,
        baseToken: Token.OMG,
    });

