/**
 * Retrieves the current pricepoint for two currencies.
 * @param fstCode The first currency.
 * @param sndCode The second currency.
 * @returns An array containing the price with respect to the currencies, and the 24 hour percent change.
 */
export async function getPrice(quoteCode: TokenCode, baseCode: TokenCode): Promise<[number, number]> {
    const quoteDetails = TokenDetails.get(quoteCode, undefined);
    const baseDetails = TokenDetails.get(baseCode, undefined);

    if (quoteDetails === undefined || baseDetails === undefined) {
        return [0, 0];
    }

    const url = `https://api.coingecko.com/api/v3/coins/${baseDetails.coingeckoID}?localization=false&tickers=false&market_data=true&community_data=false&developer_data=false&sparkline=false`;
    const response = await fetch(url);
    const data = await response.json();
    let price: number | null = data.market_data.current_price[quoteDetails.symbol.toLowerCase()];
    let percentChange: number | null = data.market_data.price_change_percentage_24h_in_currency[quoteDetails.symbol.toLowerCase()];
    if (percentChange === null) {
        percentChange = 0;
    }
    if (price === null) {
        price = 0;
    }
    return [price, percentChange];
}

export const RenExTokens = (tokens: List<TokenCode>) => tokens.filter((token: TokenCode) => {
    // If token is not found, use UnknownToken which has no settlements
    return TokenDetails.get(token, UnknownToken).settlements.indexOf(OrderSettlement.RenEx) !== -1;
}).toList();

export const RenExAtomicTokens = (tokens: List<TokenCode>) => tokens.filter((token: TokenCode) => {
    // If token is not found, use UnknownToken which has no settlements
    return TokenDetails.get(token, UnknownToken).settlements.indexOf(OrderSettlement.RenExAtomic) !== -1;
}).toList();
