import * as React from "react";

import { connect } from "react-redux";
import { bindActionCreators, Dispatch } from "redux";

import { setAlert, SetAlertAction } from "../../actions/alert/alertActions";
import { Wallet } from "../../lib/wallets/wallet";
import { Alert, AlertType } from "../../reducers/types";

interface VerificationPopupProps {
    address: string;
    wallet: Wallet;
    closePopup: () => void;
    actions: {
        setAlert: SetAlertAction;
    };
}

interface VerificationPopupState {
}

/**
 * VerificationPopup is a popup component for submitting KYC applications
 */
class VerificationPopup extends React.Component<VerificationPopupProps, VerificationPopupState> {
    // tslint:disable-next-line:no-any
    private handler: any;

    public componentDidMount(): void {
        const { closePopup } = this.props;

        // tslint:disable-next-line:no-any
        this.handler = new (window as any).Wyre.Widget({
            accountId: window.WYRE_KEY,
            auth: { type: "metamask" },
            operation: {
                type: "none"
            },
            onSuccess: () => {
                closePopup();
                this.props.actions.setAlert({
                    alert: new Alert({ message: "Your application was successfully submitted. Check back later to view the status of your application", alertType: AlertType.Success })
                });
            },
            // tslint:disable-next-line:no-any
            onExit: (err: any) => {
                if (err != null) {
                    console.error(err);
                    closePopup();
                    this.props.actions.setAlert({
                        alert: new Alert({ message: "There was an error submitting your application. Please try again later", alertType: AlertType.Error })
                    });
                }
            }
        });
    }

    public render(): JSX.Element {
        const callbackURL = location.protocol + "//" + location.host + location.pathname;
        return <div className="popup kyc content">
            <h2>Verify your account</h2>
            <p>Please submit a KYC application, or check back later to see if your application has been approved.</p>
            <div className="popup--buttons">
                <a href={`https://kyber.network/oauth/authorize?client_id=${window.KYBER_KEY}&redirect_uri=${callbackURL}&response_type=code&state=${this.props.address},${this.props.wallet}`}>
                    <button className="kyber--button"><span>Login with Kyber</span></button>
                </a>
                <button className="wyre--button" onClick={this.openWyreHandler}><span>Verify with Wyre</span></button>
            </div>
            <p>Note: Wyre currently supports US residents only.</p>
        </div>;
    }

    public openWyreHandler = () => {
        this.handler.open();
    }
}

function mapDispatchToProps(dispatch: Dispatch): { actions: VerificationPopupProps["actions"] } {
    return {
        actions: bindActionCreators({
            setAlert,
        }, dispatch)
    };
}

export default connect(null, mapDispatchToProps)(VerificationPopup);
