import { ActionType, getType } from "typesafe-actions";

import * as marketActions from "../../actions/market/marketActions";

import { MarketPrice, MarketPriceData } from "../../reducers/types";

export type MarketAction = ActionType<typeof marketActions>;

export default function marketReducer(state: MarketPriceData = new MarketPriceData(), action: MarketAction) {
    switch (action.type) {
        case getType(marketActions.updatePrices):
            const { price, percentChange } = action.payload;
            const marketPrice: MarketPrice = { price, percentChange };
            return state.set("marketPrices", state.marketPrices.set(action.payload.pair, marketPrice));
        default:
            return state;
    }
}
