import { TraderOrder } from "@renex/renex";
import { ActionType, getType } from "typesafe-actions";

import * as traderBookActions from "../../actions/orderbook/traderBookActions";

import { TraderBookData } from "../../reducers/types";
import { TraderOrderRecord } from "../../reducers/typesSdk";

export type TraderBookAction = ActionType<typeof traderBookActions>;

export default function traderBookReducer(state: TraderBookData = new TraderBookData(), action: TraderBookAction) {
    switch (action.type) {
        case getType(traderBookActions.addOrders):
            const toAdd = action.payload;
            let traderOrders = state.traderOrders;
            toAdd.map((traderOrder: TraderOrderRecord) => {
                traderOrders = traderOrders.set(traderOrder.id, traderOrder);
            });
            return state.set("traderOrders", traderOrders);
        case getType(traderBookActions.updateOrderStatus):
            let order = state.traderOrders.get(action.payload.orderID, undefined);
            if (!order) {
                return state;
            }
            order = order.set("status", action.payload.orderStatus);
            return state.set("traderOrders", state.traderOrders.set(order.id, order));
        case getType(traderBookActions.updateOrderCancelling):
            return state.set("cancelling", state.cancelling.set(action.payload.orderID, action.payload.orderCancelling));
        default:
            return state;
    }
}
