import * as Sentry from "@sentry/browser";

import { OrderedMap } from "immutable";
import { Dispatch } from "redux";
import { createStandardAction } from "typesafe-actions";

import RenExSDK, { OrderStatus } from "@renex/renex";

import { setAlert } from "../../actions/alert/alertActions";
import { addPendingAlert } from "../../actions/alert/pendingAlertActions";
import { Alert, AlertType } from "../../reducers/types";
import { MatchDetailsRecord, TraderOrderRecord } from "../../reducers/typesSdk";

interface SyncTraderOrdersPayload { traderOrders: OrderedMap<string, TraderOrderRecord>; }
export type SyncTraderOrdersAction = (payload: SyncTraderOrdersPayload) => void;
export const syncTraderOrders = createStandardAction("SYNC_TRADER_ORDERS")<SyncTraderOrdersPayload>();

type AddOrdersPayload = TraderOrderRecord[];
export type AddOrdersAction = (payload: AddOrdersPayload) => void;
export const addOrders = createStandardAction("ADD_ORDERS")<AddOrdersPayload>();

interface UpdateOrderStatusPayload { orderID: string; orderStatus: OrderStatus; }
export type UpdateOrderStatusAction = (payload: UpdateOrderStatusPayload) => void;
export const updateOrderStatus = createStandardAction("UPDATE_ORDER")<UpdateOrderStatusPayload>();

interface UpdateOrderCancellingPayload { orderID: string; orderCancelling: boolean; }
export type UpdateOrderCancellingAction = (payload: UpdateOrderCancellingPayload) => void;
export const updateOrderCancelling = createStandardAction("UPDATE_ORDER_CANCELLINGs")<UpdateOrderCancellingPayload>();

interface CancelLocalOrderPayload { orderID: string; }
export type CancelLocalOrderAction = (payload: CancelLocalOrderPayload) => void;
export const cancelLocalOrder = createStandardAction("CANCEL_LOCAL_ORDER")<CancelLocalOrderPayload>();

/**
 * Filter for orders that don't need to be checked - based on network, address
 * and status
 */
export const defaultOrderFilter = (address: string, checkAllOrders: boolean) => ((order: TraderOrderRecord | undefined) => {
    if (order === undefined) {
        return false;
    }

    // Check that the order belongs to the current account
    if (order.trader.toLowerCase() !== address.toLowerCase()) {
        return false;
    }

    if (!checkAllOrders) {
        if (order.status !== OrderStatus.OPEN &&
            order.status !== OrderStatus.NOT_SUBMITTED &&
            order.status !== OrderStatus.CONFIRMED
        ) {
            return false;
        }
    }

    return true;
});

export type CheckOrderStatusesAction = (
    sdk: RenExSDK,
    traderOrders: OrderedMap<string, TraderOrderRecord>,
    checkAllOrders?: boolean,
) => (dispatch: Dispatch) => Promise<void>;
export const checkOrderStatuses: CheckOrderStatusesAction = (
    sdk, traderOrders, checkAllOrders = false
) => async (dispatch) => {

    // Filter if order is for different network or trader
    const toCheck = traderOrders
        .toArray()
        .filter(defaultOrderFilter(sdk.getAddress(), checkAllOrders));

    for (let order of toCheck) {
        if (order === undefined) { return; }

        // Check the status of the order in the orderbook.
        // const hexOrderID = new EncodedData(order.id, Encodings.BASE64).toHex();
        let orderStatus;
        try {
            orderStatus = await sdk.fetchOrderStatus(order.id);
        } catch (error) {
            console.error(error);
            Sentry.captureException(error);
            continue;
        }

        // If the order is settled and doesn't have match details, get them now
        if (!order.matchDetails && orderStatus === OrderStatus.SETTLED) {
            order = order.set("matchDetails", new MatchDetailsRecord(await sdk.fetchMatchDetails(order.id)));
            order = order.set("status", OrderStatus.SETTLED);

            // Only update order after the balances have been updated
            const alert = async () => {
                dispatch(addOrders([order]));

                dispatch(setAlert({
                    alert: new Alert({
                        alertType: AlertType.Success,
                        // TODO: Replace with execution details
                        message: "Your order has successfully been settled",
                    })
                }));
            };
            dispatch(addPendingAlert({ id: `settled: ${order.id}`, method: alert }));

        } else if (order.status !== orderStatus) {
            dispatch(updateOrderStatus({ orderID: order.id, orderStatus }));
        }
    }
};
