import { combineReducers } from "redux";

import alert from "./alert/alertReducer";
import balanceHistory from "./balances/balancesReducer";
import inputs from "./inputs/inputsReducer";
import marketPrices from "./market/marketReducer";
import hiddenBook from "./orderbook/hiddenBookReducer";
import traderBook from "./orderbook/traderBookReducer";
import popup from "./popup/popupReducer";
import sdk from "./sdk/sdkReducer";
import trader from "./trader/traderReducer";

import { ApplicationData } from "./types";

const rootReducer = combineReducers<ApplicationData>({
    alert,
    balanceHistory,
    hiddenBook,
    inputs,
    marketPrices,
    popup,
    traderBook,
    trader,
    sdk,
});

export default rootReducer;
