import * as Sentry from "@sentry/browser";
import * as React from "react";

import axios from "axios";
import Web3 from "web3";

import { connect } from "react-redux";
import { bindActionCreators, Dispatch } from "redux";

import RenExSDK from "@renex/renex";

import Header from "../../components/Header";
import HiddenOrderbook from "../../components/HiddenOrderbook";
import Loading from "../../components/Loading";
import MarketBanner from "../../components/MarketBanner";
import Wallets from "../../components/Wallets";

import { setAlert, SetAlertAction } from "../../actions/alert/alertActions";
import { login, LoginAction } from "../../actions/trader/accountActions";
import { storeWallet, StoreWalletAction } from "../../actions/trader/walletActions";
import { Wallet, WalletDetails } from "../../lib/wallets/wallet";
import { includesAddress } from "../../lib/web3";
import { Alert, AlertType, ApplicationData } from "../../reducers/types";


interface HomeProps {
    sdk: RenExSDK;
    url: string | null;
    actions: {
        login: LoginAction;
        storeWallet: StoreWalletAction;
        setAlert: SetAlertAction;
    };
}

interface HomeState {
    checkingVerification: boolean;
}

/**
 * Home is a page whose principal components are wallet selection to allow users
 * to log-in, and the hidden orderbook
 */
class Home extends React.Component<HomeProps, HomeState> {
    public constructor(props: HomeProps, context: object) {
        super(props, context);
        this.state = {
            checkingVerification: false,
        };
    }

    public async componentDidMount(): Promise<void> {
        this.props.actions.storeWallet({ wallet: null });

        if (window.location.href.indexOf("?code=") !== -1) {
            // Show loading state between verification checks
            this.setState({ checkingVerification: true });

            // Check verification
            const { verified, address, kyberUID, wallet, alert } = await this.kyberVerification();
            if (verified && address !== undefined && kyberUID !== undefined && wallet !== undefined) {
                await this.handleForwardLogin(address, wallet);
            } else if (!verified && alert) {
                this.props.actions.setAlert({ alert });
            }
            this.setState({ checkingVerification: false });
        }
    }

    public render(): JSX.Element {
        const { checkingVerification } = this.state;
        return (
            <div className="home">
                {checkingVerification &&
                    <>
                        <div className="popup">
                            <Loading />
                        </div>
                        <div className="overlay" />
                    </>
                }
                <MarketBanner />
                <Header withMenu={false} />
                <Wallets />
                <div className="content">
                    <HiddenOrderbook />
                </div>
            </div>
        );
    }

    private async kyberVerification(): Promise<{ verified: boolean, address?: string, kyberUID?: string, wallet?: Wallet, alert?: Alert }> {
        const authCode = getParameterByName("code");
        const state = getParameterByName("state");
        if (state) {
            const address = state.split(",")[0];
            const wallet: Wallet = parseInt(state.split(",")[1], undefined);
            const redirectURL = location.protocol + "//" + location.host + location.pathname;
            const data = {
                address,
                request: {
                    grant_type: "authorization_code",
                    code: authCode,
                    redirect_uri: redirectURL,
                    client_id: window.KYBER_KEY
                }
            };
            try {
                const response = await axios.post(window.NETWORK.ingress + "/kyber", JSON.stringify(data));
                const kyberUID = response.data.uid;
                const lowerCaseAddresses = response.data.active_wallets.map((value: string) => value.toLowerCase());
                if (!address) {
                    throw new Error("Unable to retrieve address from Kyber response");
                }
                if (response.data.kyc_status !== "approved") {
                    throw new Error(`Received unexpected Kyber status: ${response.data.kyc_status}`);
                }
                if (lowerCaseAddresses.indexOf(address.toLowerCase()) === -1) {
                    throw new Error(`Address ${address} not authorized with Kyber`);
                }
                return { verified: true, address, kyberUID, wallet };
            } catch (error) {
                let alert: Alert;
                if (error.response && error.response.status === 401) {
                    alert = new Alert({
                        message: `Your Kyber account has not been verified with address ${address}. Please verify and check back later`
                    });
                } else {
                    console.error(error);
                    Sentry.captureException(error);
                    alert = new Alert({
                        message: `Unable to retrieve account verification details. Please try again later`
                    });
                }
                return { verified: false, alert };
            }
        } else {
            console.error("Not enough information for Kyber KYC");
            return { verified: false };
        }
    }

    private async handleForwardLogin(address: string, wallet: Wallet): Promise<void> {
        const { actions, sdk, url } = this.props;
        const walletDetails = WalletDetails.get(wallet, undefined);
        const [provider,] = await walletDetails.getWeb3Provider(address);

        if (await includesAddress(new Web3(provider), address)) {
            actions.login(sdk, provider, address, { redirect: true, url });
            actions.setAlert({
                alert: new Alert({
                    message: "Successfully logged in via Kyber verification",
                    alertType: AlertType.Success
                })
            });
            return;
        }

        throw new Error("Logged in to a non-verified address.");
    }
}

// Adapted from https://stackoverflow.com/a/901144
export function getParameterByName(name: string) {
    name = name.replace(/[\[\]]/g, "\\$&");
    const regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)");
    const results = regex.exec(window.location.href);
    if (!results) {
        return null;
    }
    if (!results[2]) {
        return "";
    }
    return decodeURIComponent(results[2].replace(/\+/g, " "));
}

function mapStateToProps(state: ApplicationData) {
    return {
        sdk: state.sdk.sdk,
        url: state.trader.url,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: HomeProps["actions"] } {
    return {
        actions: bindActionCreators({
            login,
            storeWallet,
            setAlert,
        }, dispatch)
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);
