import * as Sentry from "@sentry/browser";
import BigNumber from "bignumber.js";

import { Dispatch } from "redux";

import RenExSDK, { OrderInputs, OrderSettlement, OrderSide, OrderStatus, OrderType, SimpleConsole } from "@renex/renex";

import { setAlert } from "../../actions/alert/alertActions";
import { clearPopup, setPopup } from "../../actions/popup/popupActions";
import { newApprovalPopup } from "../../components/popups/ApprovalPopup";
import { readableToBalance } from "../../lib/balances";
import { Pair, Pairs } from "../../lib/market";
import { ErrorCanceledByUser } from "../../lib/wallets/wallet";
import { Alert, AlertType } from "../../reducers/types";

import { addPendingAlert } from "../../actions/alert/pendingAlertActions";
import { getBalances } from "../../actions/balances/balancesActions";
import { addOrders, updateOrderCancelling, updateOrderStatus } from "../../actions/orderbook/traderBookActions";
import { newOpenOrderPopup } from "../../components/popups/OpenOrderPopup";
import { traderOrderToRecord } from "../../reducers/typesSdk";

export const ErrorUnableToOpen = "Unable to open order";
export const ErrorUnableToCancel = "Unable to cancel order";

export type OpenOrderAction = (
    sdk: RenExSDK,
    market: Pair,
    side: OrderSide,
    pricepoint: string,
    volume: string,
    minimumVolume: string,
    orderType: OrderType,
    onSuccess?: () => void,
    onError?: () => void,
) => (dispatch: Dispatch) => Promise<void>;
export const openOrder: OpenOrderAction = (
    sdk, market, side, pricepoint, volume, minimumVolume, orderType, onSuccess, onError
) => async (dispatch) => {
    const marketPair = Pairs.get(market, undefined);
    if (!marketPair) {
        return;
    }
    const skipConfirm = marketPair.orderSettlement === OrderSettlement.RenExAtomic;

    const pricepointBN = new BigNumber(pricepoint);
    const volumeBN = new BigNumber(volume);
    const minimumVolumeBN = new BigNumber(minimumVolume);

    const orderInputs: OrderInputs = {
        symbol: `${marketPair.baseToken}/${marketPair.quoteToken}`,
        side,
        price: pricepointBN.toFixed(),
        volume: volumeBN,
        minVolume: minimumVolumeBN,
        type: orderType,
    };

    const openOrderCall = (simpleConsole?: SimpleConsole) => {
        return sdk.openOrder(orderInputs, { simpleConsole, awaitConfirmation: false })
            .then(async (openOrderResult) => {
                const { traderOrder } = openOrderResult;
                dispatch(addOrders([traderOrderToRecord(traderOrder)]));

                // Reload balances before closing popup
                await getBalances(sdk)(dispatch);

                dispatch(setAlert({
                    alert: new Alert({
                        alertType: AlertType.Success,
                        message: "Your order has been sent",
                    })
                }));

                dispatch(clearPopup());
                if (onSuccess) {
                    onSuccess();
                }
            })
            .catch(async (error: Error) => {
                if (onError) {
                    onError();
                }
                if (error.message.match("User denied transaction signature")) {
                    throw new Error(ErrorCanceledByUser);
                }
                Sentry.captureException(error);
                throw error;
            });
    };

    const onDismiss = () => {
        dispatch(clearPopup());
        if (onError) {
            onError();
        }
    };

    dispatch(setPopup(newOpenOrderPopup(orderInputs, openOrderCall, onDismiss, skipConfirm)));
};

export type CancelOrderAction = (
    sdk: RenExSDK,
    orderID: string,
) => (dispatch: Dispatch) => Promise<undefined | {}>;
export const cancelOrder: CancelOrderAction = (sdk, orderID) => async (dispatch) => {

    return new Promise((resolve: () => void) => {

        const close = () => {
            dispatch(clearPopup());
            resolve();
        };

        dispatch(updateOrderCancelling({ orderID, orderCancelling: true }));
        const cancelOrderCall = async () => {
            const { promiEvent } = await sdk.cancelOrder(orderID, { awaitConfirmation: false });
            if (!promiEvent) {
                throw new Error("Unable to cancel order");
            }
            return promiEvent
                .on("transactionHash", async () => {
                    // Transaction is sent
                    close();
                })
                .then(() => {
                    // Transaction is confirmed
                    dispatch(updateOrderStatus({ orderID, orderStatus: OrderStatus.CANCELED }));

                    const alert = async () => {
                        dispatch(setAlert({
                            alert: new Alert({
                                alertType: AlertType.Success,
                                message: "Your order has been cancelled",
                            })
                        }));
                        dispatch(updateOrderCancelling({ orderID, orderCancelling: false }));
                    };
                    dispatch(addPendingAlert({ id: `cancel: ${orderID}`, method: alert }));
                })
                .catch((error: Error) => {
                    // Transaction failed

                    dispatch(updateOrderCancelling({ orderID, orderCancelling: false }));
                    if (error.message.match("User denied transaction signature")) {
                        throw new Error(ErrorCanceledByUser);
                    }
                    console.error(error);
                    Sentry.captureException(error);
                    throw new Error(ErrorUnableToCancel);
                });
        };

        dispatch(setPopup(newApprovalPopup(cancelOrderCall, close)));

    });

};
