import moment from "moment";
import * as React from "react";

import RenExSDK, { OrderSettlement, OrderSide, OrderStatus, TraderOrder } from "@renex/renex";

import { Map, OrderedMap } from "immutable";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { Dispatch } from "redux";
import { bindActionCreators } from "redux";

import Heading from "./Heading";
import Loading from "./Loading";

import { setAlert, SetAlertAction } from "../actions/alert/alertActions";
import { cancelOrder, CancelOrderAction } from "../actions/trader/orderActions";
import { getTimeMagnitude, naturalTime, TimeMagnitude } from "../lib/conversion";
import { TokenDetails, UnknownToken } from "../lib/market";
import { Alert, AlertType, ApplicationData, LabelType, Settlements } from "../reducers/types";
import { InfoLabel } from "./InfoLabel";

// This is the number of orders that will be displayed if the 'recent' prop
// is set.
export const RECENT_AMOUNT = 5;

interface StoreProps {
    cancelling: Map<string, boolean>;
    traderOrders: OrderedMap<string, TraderOrder>;
    sdk: RenExSDK;
}

interface SubmittedTradesProps extends StoreProps {
    recent?: boolean;
    actions: {
        cancelOrder: CancelOrderAction;
        setAlert: SetAlertAction;
    };
}

interface SubmittedTradesState {
    relevantRows: number;
    highlightLatest: boolean;
    receivedOrders: boolean;
}

class SubmittedTrades extends React.Component<SubmittedTradesProps, SubmittedTradesState> {
    private timeout: NodeJS.Timer | undefined;
    private rerender: NodeJS.Timer | undefined;

    public constructor(props: SubmittedTradesProps, context: object) {
        super(props, context);
        this.state = {
            highlightLatest: false,
            receivedOrders: false,
            relevantRows: 0,
        };
    }

    public componentWillReceiveProps(nextProps: SubmittedTradesProps): void {
        // Highlight the latest order if it was just added.
        if (this.props.traderOrders.size < nextProps.traderOrders.size) {
            if (this.state.receivedOrders) {
                this.setState({ highlightLatest: true });

                if (this.timeout) { clearTimeout(this.timeout); }
                this.timeout = setTimeout(
                    () => this.setState({ highlightLatest: false }),
                    3 * 1000,
                );
            } else {
                this.setState({ receivedOrders: true });
            }
        } else if (this.props.traderOrders.size > nextProps.traderOrders.size) {
            // Remove highlight if order failed and has been removed from the list.
            this.setState({ highlightLatest: false });
        }
    }

    public componentWillUnmount(): void {
        if (this.timeout) { clearTimeout(this.timeout); }
        if (this.rerender) { clearTimeout(this.rerender); }
    }

    public render(): JSX.Element {
        const { recent } = this.props;
        const { traderOrders } = this.props;
        let relevantRows = null;
        if (traderOrders.size > 0) {
            // Find the most precise time being shown (seconds, minutes, hours...)
            // and rerender to ensure that these times are updated
            const magnitude = traderOrders
                .map((traderOrder: TraderOrder) => getTimeMagnitude(traderOrder.computedOrderDetails.date))
                .reduce((val: number, min: number) => Math.min(val, min), TimeMagnitude.Day);

            if (this.rerender) { clearTimeout(this.rerender); }
            this.rerender = setTimeout(() => {
                this.forceUpdate();
            }, magnitude / 2);

            relevantRows = this.generateRows();
        }

        const title = `Your ${recent ? "latest " : ""}submitted orders`;

        return (
            <div className="section trades">
                <div className="container">
                    <Heading title={title} />
                    <div className="table--wrapper">
                        <table>
                            <thead>
                                <tr>
                                    {/* If any fields are added/removed, the colSpan
                                    below must be modified to reflect this. */}
                                    <th>Order ID</th>
                                    <th>From</th>
                                    <th>To</th>
                                    <th>Price</th>
                                    <th>Opened</th>
                                    <th>Status</th>
                                    <th>Cancel?</th>
                                </tr>
                            </thead>
                            <tbody>
                                {relevantRows ?
                                    recent ? relevantRows.slice(0, RECENT_AMOUNT) : relevantRows
                                    :
                                    <tr>
                                        <td colSpan={8}>No submitted orders</td>
                                    </tr>
                                }
                            </tbody>
                        </table>
                    </div>
                    {recent && relevantRows && relevantRows.length - RECENT_AMOUNT > 0 &&
                        <p><Link to="/orders">View {relevantRows.length - RECENT_AMOUNT} more</Link></p>
                    }
                </div>
            </div>
        );
    }

    private async handleCancel(orderID: string): Promise<void> {
        const { sdk, traderOrders } = this.props;

        if (!sdk || !traderOrders) {
            return;
        }

        // Attempt to cancel
        try {
            await this.props.actions.cancelOrder(sdk, orderID);
        } catch (error) {
            console.error(error);
            this.props.actions.setAlert({
                alert: new Alert({
                    alertType: AlertType.Error,
                    // TODO: Replace with execution details
                    message: "Unable to cancel order",
                })
            });
        }
    }

    private generateRows(): JSX.Element[] | null {
        const { traderOrders, sdk, cancelling } = this.props;
        const { highlightLatest } = this.state;

        let relevantRows = false;

        const atomConnected = sdk.atom.isConnected();

        const rows = traderOrders.toArray().reverse().filter((order: TraderOrder) => {
            if (order.trader.toLowerCase() !== sdk.getAddress().toLowerCase()) {
                return false;
            }

            relevantRows = true;
            return true;
        }).map((order: TraderOrder, key: number) => {

            const spendDetails = TokenDetails.get(order.computedOrderDetails.spendToken, UnknownToken);
            const receiveDetails = TokenDetails.get(order.computedOrderDetails.receiveToken, UnknownToken);

            const fstSymbol = (order.orderInputs.side === OrderSide.SELL ? receiveDetails : spendDetails).symbol;
            const sndSymbol = (order.orderInputs.side === OrderSide.SELL ? spendDetails : receiveDetails).symbol;

            // Deduct fees from receive amount.
            // receiveAmount = receiveAmount.minus(receiveAmount.times(ORDER_FEE));

            const receive = order.computedOrderDetails.receiveVolume;
            const spend = order.computedOrderDetails.spendVolume;

            return (
                <tr key={key} className={`${order.status.toLowerCase()} ${key === 0 && highlightLatest ? "recent" : ""}`}>
                    <td><span className="monospace">{order.id}</span></td>
                    <td className="volume right">
                        <span>{spend.toString()} {spendDetails.symbol}</span>
                    </td>
                    <td className="volume right">{
                        <span>{receive.toString()} {receiveDetails.symbol}</span>
                    }</td>
                    <td className="price center">{`${order.orderInputs.price.toFixed()} ${fstSymbol} to ${sndSymbol}`}</td>
                    <td className="center" title={moment(order.computedOrderDetails.date * 1000).format("MMMM Do YYYY, HH:mm:ss")}>
                        <span>{naturalTime(order.computedOrderDetails.date, { message: "Just now", suffix: "ago", countDown: false })}</span>
                    </td>
                    <td className="center">
                        {
                            order.status === OrderStatus.NOT_SUBMITTED ?
                                <Loading /> :
                                <span>{renderOrderStatus(order.status)}
                                    {order.computedOrderDetails.orderSettlement === OrderSettlement.RenExAtomic &&
                                        order.status === OrderStatus.CONFIRMED &&
                                        !atomConnected ?
                                        <InfoLabel type={LabelType.Warning}>Unable to connect to Swapperd</InfoLabel>
                                        : null
                                    }
                                </span>
                        }
                    </td>
                    <td className="center">
                        {
                            order.status === OrderStatus.OPEN ? (
                                cancelling.get(order.id, false) ?
                                    <Loading />
                                    :
                                    <span
                                        className="cross"
                                        role="button"
                                        onClick={this.handleCancel.bind(this, order.id)}
                                    >&#x00D7;</span>
                            )
                                :
                                <span>-</span>
                        }
                    </td>
                </tr >
            );
        });

        return relevantRows ? rows : null;
    }
}

const renderOrderStatus = (status: OrderStatus) => {
    switch (status) {
        case OrderStatus.NOT_SUBMITTED: return "Pending";
        case OrderStatus.FAILED_TO_SETTLE: return "Failed";
        case OrderStatus.OPEN: return "Open";
        case OrderStatus.CONFIRMED: return "Settling";
        case OrderStatus.CANCELED: return "Canceled";
        case OrderStatus.SETTLED: return "Settled";
        case OrderStatus.SLASHED: return "Aborted";
        default:
            return "Unknown";
    }
};

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        cancelling: state.traderBook.cancelling,
        traderOrders: state.traderBook.traderOrders,
        sdk: state.sdk.sdk,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: SubmittedTradesProps["actions"] } {
    return {
        actions: bindActionCreators(
            {
                cancelOrder,
                setAlert,
            }, dispatch)
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(SubmittedTrades);
