import { ActionType, getType } from "typesafe-actions";

import * as hiddenBookActions from "../../actions/orderbook/hiddenBookActions";

import { HiddenBookData } from "../../reducers/types";

export const NUMBER_OF_ORDERS = 10;
export type HiddenBookAction = ActionType<typeof hiddenBookActions>;

export default function hiddenBookReducer(state: HiddenBookData = new HiddenBookData(), action: HiddenBookAction) {
    switch (action.type) {
        case getType(hiddenBookActions.setHiddenOrders):
            return state.set("hiddenOrders", action.payload.hiddenOrders);
        default:
            return state;
    }
}
