// tslint:disable:no-object-literal-type-assertion

import * as Sentry from "@sentry/browser";

import RenExSDK, { AtomicConnectionStatus, MarketDetails, Order, OrderSettlement, OrderSide, Token, TokenCode } from "@renex/renex";
import BigNumber from "bignumber.js";
import Web3 from "web3";

import { List, Map, OrderedMap } from "immutable";

import { Pair } from "../lib/market";
import { INFURA_URL, networkData } from "../lib/network";
import { Record } from "../lib/record";
import { Wallet } from "../lib/wallets/wallet";
import { BalanceActionRecord, TraderOrderRecord } from "../reducers/typesSdk";

export interface Serializable<T> {
    serialize(): string;
    deserialize(str: string): T;
}

export interface ApplicationData {
    hiddenBook: HiddenBookData;
    traderBook: TraderBookData;
    balanceHistory: BalanceHistory;
    trader: TraderData;
    sdk: SDKData;
    marketPrices: MarketPriceData;
    inputs: InputsData;
    alert: AlertData;
    popup: PopupData;
}

// There are some issues with storing prices and volumes as BigNumbers/numbers
// regarding rerendering, e.g. 0.0 being converted to 0 before the user has the
// chance to finish typing a decimal number. Hence they are stored as strings.
export interface OrderInputs {
    side: OrderSide;
    pricepoint: string;
    volume: string;
    minVolume: string;
}

export interface BalanceInputs {
    currency: TokenCode;
    depositAmount: string;
    withdrawAmount: string;
}

export class InputsData extends Record({
    market: Pair.ETH_REN,
    orderInputs: {
        side: OrderSide.BUY,
        pricepoint: "",
        volume: "",
        minVolume: "",
    } as OrderInputs,
    balanceInputs: {
        currency: Token.ETH,
        depositAmount: new BigNumber(0).toFixed(),
        withdrawAmount: new BigNumber(0).toFixed(),
    } as BalanceInputs,
}) { }

export class HiddenBookData extends Record({
    hiddenOrders: null as List<Order> | null,
}) { }

export class TraderBookData extends Record({
    traderOrders: OrderedMap<string, TraderOrderRecord>(),
    cancelling: Map<string, boolean>(),
}) { }

export class BalanceHistory extends Record({
    updateIndex: 0,
    balanceHistory: OrderedMap<string, BalanceActionRecord>(),
}) { }

export class TraderData extends Record({
    address: null as string | null,
    url: null as string | null,
    agreedToTerms: false,

    wallet: null as Wallet | null,
    atomicBalances: OrderedMap<TokenCode, BigNumber | null>(),
    usableAtomicBalances: OrderedMap<TokenCode, BigNumber | null>(),
    atomicAddresses: OrderedMap<TokenCode, string>(),
    atomConnectionStatus: AtomicConnectionStatus.NotConnected,

    balances: OrderedMap<TokenCode, BigNumber | null>(),
    accountBalances: OrderedMap<TokenCode, BigNumber | null>(),
    usableBalances: OrderedMap<TokenCode, BigNumber | null>(),
}) implements Serializable<TraderData> {
    public serialize(): string {
        return JSON.stringify({
            address: this.address,
            agreedToTerms: this.agreedToTerms,
        });
    }

    public deserialize(str: string): TraderData {
        let next = this;
        try {
            const data = JSON.parse(str);
            next = next.set("address", data.address);
            next = next.set("agreedToTerms", data.agreedToTerms);
        } catch (err) {
            console.error(err);
            Sentry.captureException(`cannot deserialize local storage: ${err}`);
        }
        return next;
    }
}

window.sdk = new RenExSDK(new Web3.providers.HttpProvider(INFURA_URL), networkData);

export class SDKData extends Record({
    sdk: window.sdk,
    supportedTokens: List() as List<TokenCode>,
    supportedAtomicTokens: List() as List<TokenCode>,
    supportedMarkets: List() as List<MarketDetails>,
    minEthTradeVolume: new BigNumber(0),
    darknodeFeePercent: new BigNumber(0),
    wrappingFeePercent: new BigNumber(0),
}) { }

export type Settlements = OrderedMap<OrderSettlement, boolean>;

export interface MarketPrice {
    price: number;
    percentChange: number;
}

export const UnknownMarketPrice: MarketPrice = {
    price: 0,
    percentChange: 0,
};

export class MarketPriceData extends Record({
    marketPrices: OrderedMap<Pair, MarketPrice>(),
}) { }

export enum AlertType {
    Error = "error",
    Warning = "warning",
    Success = "success"
}

export enum LabelType {
    Info = "info",
    Warning = "warning"
}

export class Alert extends Record({
    alertType: AlertType.Warning,
    message: "", // TODO: Allow for links
}) { }

export class AlertData extends Record({
    pendingAlerts: Map<string, () => Promise<void>>(),
    alert: { message: "" } as Alert
}) { }

export class PopupData extends Record({
    popup: null as JSX.Element | null,
    dismissible: true,
    onCancel: (() => null) as () => void,
}) { }
