import BigNumber from "bignumber.js";

import { BalanceAction, BalanceActionType, ComputedOrderDetails, MatchDetails, OrderInputsAll, OrderSettlement, OrderSide, OrderStatus, OrderType, TraderOrder, TransactionStatus } from "@renex/renex";
import BN from "bn.js";

import { Record } from "../lib/record";

export class OrderInputsAllRecord extends Record({
    symbol: "",
    baseToken: "",
    quoteToken: "",
    side: OrderSide.BUY,
    price: new BigNumber(0),
    volume: new BigNumber(0),
    minVolume: new BigNumber(0),
    type: OrderType.LIMIT,
    orderSettlement: OrderSettlement.RenEx,
    nonce: new BN(0),
    expiry: 0,
}) implements OrderInputsAll { }

export class MatchDetailsRecord extends Record({
    orderID: "",
    matchedID: "",

    receivedVolume: new BigNumber(0),
    spentVolume: new BigNumber(0),
    fee: new BigNumber(0),
    receivedToken: "",
    spentToken: "",
}) implements MatchDetails { }

export class ComputedOrderDetailsRecord extends Record({
    spendToken: "",
    receiveToken: "",
    receiveVolume: new BigNumber(0),
    spendVolume: new BigNumber(0),
    date: 0,
    feeAmount: new BigNumber(0),
    feeToken: "",
    orderSettlement: OrderSettlement.RenEx,
    nonce: new BN(0),
}) implements ComputedOrderDetails { }

const TraderOrderRecordDefault = {
    id: "",
    trader: "",
    status: OrderStatus.NOT_SUBMITTED,
    matchDetails: undefined as MatchDetailsRecord | undefined,

    computedOrderDetails: new ComputedOrderDetailsRecord(),
    orderInputs: new OrderInputsAllRecord(),
    transactionHash: "",
};
export class TraderOrderRecord extends Record(TraderOrderRecordDefault) implements TraderOrder { }
// TODO: Does Typescript have a way of getting this without explicitly defining it?
export type TraderOrderRecordInner = typeof TraderOrderRecordDefault;

export class BalanceActionRecord extends Record({
    action: BalanceActionType.Withdraw,
    amount: new BigNumber(0),
    time: 0,
    status: TransactionStatus.Pending,
    token: "",
    trader: "",
    txHash: "",
    nonce: undefined as number | undefined,
}) implements BalanceAction { }

export const traderOrderToRecord = (traderOrder: TraderOrder): TraderOrderRecord => {
    const inner: TraderOrderRecordInner = {
        id: traderOrder.id,
        trader: traderOrder.trader,
        status: traderOrder.status,
        matchDetails: traderOrder.matchDetails ? new MatchDetailsRecord(traderOrder.matchDetails) : undefined,

        computedOrderDetails: new ComputedOrderDetailsRecord(traderOrder.computedOrderDetails),
        orderInputs: new OrderInputsAllRecord(traderOrder.orderInputs),
        transactionHash: traderOrder.transactionHash,
    };
    // tslint:disable-next-line:no-object-literal-type-assertion
    return new TraderOrderRecord(inner);
};
