import * as React from "react";

import { List } from "immutable";
import { connect } from "react-redux";

import RenExSDK, { Order } from "@renex/renex";

import Heading from "./Heading";
import Loading from "./Loading";

import { HiddenOrderStatusDisplay } from "../lib/globals";
import { NUMBER_OF_ORDERS } from "../reducers/orderbook/hiddenBookReducer";
import { ApplicationData } from "../reducers/types";

interface StoreProps {
    hiddenOrders: List<Order> | null;
    sdk: RenExSDK;
}

interface HiddenOrderbookProps extends StoreProps {
}

/**
 * HiddenOrderbook is a visual component for displaying
 */
class HiddenOrderbook extends React.Component<HiddenOrderbookProps> {
    public constructor(props: HiddenOrderbookProps, context: object) {
        super(props, context);
    }

    public render(): JSX.Element {
        const { hiddenOrders } = this.props;
        return (
            <div className="section orderbook">
                <div className="container">
                    <Heading title="Hidden orderbook" description={`Last ${NUMBER_OF_ORDERS} orders`} />
                    {hiddenOrders ?
                        <div className="table--wrapper">
                            <table>
                                <thead>
                                    <tr>
                                        <th>Order ID</th>
                                        <th className="center">Status</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {hiddenOrders.size > 0 ?
                                        hiddenOrders.reverse().toArray().map((order: Order) =>
                                            <tr key={order.id}>

                                                <td className="truncate"><span className="monospace">{order.id}</span></td>
                                                <td className="center">{HiddenOrderStatusDisplay[order.status]}</td>
                                            </tr>
                                        ) :
                                        <tr />
                                    }
                                </tbody>
                            </table>
                        </div>
                        :
                        <Loading />
                    }
                </div>
            </div>
        );
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        hiddenOrders: state.hiddenBook.hiddenOrders,
        sdk: state.sdk.sdk,
    };
}

export default connect(mapStateToProps)(HiddenOrderbook);
