import * as React from "react";

import BigNumber from "bignumber.js";
import Web3 from "web3";

import { List, OrderedMap } from "immutable";
import { connect } from "react-redux";
import { Redirect, Route, RouteComponentProps, Router, withRouter } from "react-router-dom";
import { Dispatch } from "redux";
import { bindActionCreators } from "redux";

import RenExSDK, { BalanceAction, TokenCode, TraderOrder } from "@renex/renex";

import history from "../lib/history";
import Alerts from "./Alerts";
import Balances from "./pages/Balances";
import Exchange from "./pages/Exchange";
import Home from "./pages/Home";
import LoggingOut from "./pages/LoggingOut";
import Orders from "./pages/Orders";
import Popup from "./Popup";

import { setAlert, SetAlertAction } from "../actions/alert/alertActions";
import { GetAtomicBalanceAction, getAtomicBalances, GetBalanceAction, getBalances } from "../actions/balances/balancesActions";
import { updateBalancesHistory, UpdateBalancesHistoryAction } from "../actions/balances/balancesActions";
import { updatePrices, UpdatePricesAction } from "../actions/market/marketActions";
import { getHiddenOrders, GetHiddenOrdersAction } from "../actions/orderbook/hiddenBookActions";
import { checkOrderStatuses, CheckOrderStatusesAction } from "../actions/orderbook/traderBookActions";
import { clearPopup, ClearPopupAction, setPopup, SetPopupAction } from "../actions/popup/popupActions";
import { storeSDKData, StoreSDKDataAction } from "../actions/sdk/sdkActions";
import { refreshAtomConnectionStatus, RefreshAtomConnectionStatusAction } from "../actions/settlement/atomicActions";
import { login, LoginAction, logout, LogoutAction, lookForLogout, LookForLogoutAction, storeURL, StoreURLAction } from "../actions/trader/accountActions";
import { getPrice, Pairs } from "../lib/market";
import { Wallet } from "../lib/wallets/wallet";
import { getInjectedWeb3Provider } from "../lib/wallets/web3browser";
import { includesAddress } from "../lib/web3";
import { Alert, AlertData, AlertType, ApplicationData } from "../reducers/types";
import { BalanceActionRecord, TraderOrderRecord } from "../reducers/typesSdk";
import { newTermsPopup } from "./popups/TermsPopup";

interface StoreProps {
    address: string | null;
    agreedToTerms: boolean;
    balanceHistory: OrderedMap<string, BalanceActionRecord>;
    pendingAlerts: AlertData["pendingAlerts"];
    sdk: RenExSDK;
    traderOrders: OrderedMap<string, TraderOrderRecord>;
    usableAtomicBalances: OrderedMap<TokenCode, BigNumber | null>;
    usableBalances: OrderedMap<TokenCode, BigNumber | null>;
    wallet: Wallet | null;
    url: string | null;
}

interface AppProps extends StoreProps {
    actions: {
        checkOrderStatuses: CheckOrderStatusesAction;
        clearPopup: ClearPopupAction;
        getAtomicBalances: GetAtomicBalanceAction;
        getBalances: GetBalanceAction;
        getHiddenOrders: GetHiddenOrdersAction;
        login: LoginAction;
        logout: LogoutAction;
        lookForLogout: LookForLogoutAction;
        refreshAtomConnectionStatus: RefreshAtomConnectionStatusAction;
        setAlert: SetAlertAction;
        setPopup: SetPopupAction;
        storeSDKData: StoreSDKDataAction;
        storeURL: StoreURLAction;
        updateBalancesHistory: UpdateBalancesHistoryAction;
        updatePrices: UpdatePricesAction;
    };
}

interface AppState {
    checkingReLogin: boolean;
}

// Scroll restoration based on https://reacttraining.com/react-router/web/guides/scroll-restoration
const ScrollToTop = withRouter(
    // tslint:disable-next-line:no-any
    class ScrollToTopWithoutRouter extends React.Component<RouteComponentProps<any>> {
        // tslint:disable-next-line:no-any
        public componentDidUpdate(prevProps: Readonly<RouteComponentProps<any>>): void {
            if (this.props.location !== prevProps.location) {
                window.scrollTo(0, 0);
            }
        }

        public render(): JSX.Element | null {
            return null;
        }
    }
);

/**
 * App is the main visual component responsible for displaying different routes
 * and running background app loops
 */
class App extends React.Component<AppProps, AppState> {
    private setupLoopsTimeout: NodeJS.Timer | undefined;
    private callGetBalancesTimeout: NodeJS.Timer | undefined;
    private callHiddenOrdersTimeout: NodeJS.Timer | undefined;
    private callUpdatePricesTimeout: NodeJS.Timer | undefined;
    private callLookForLogoutTimeout: NodeJS.Timer | undefined;
    private callConnectToAtomTimeout: NodeJS.Timer | undefined;
    private callUpdateAtomStatusTimeout: NodeJS.Timer | undefined;
    private callGetAtomicBalancesTimeout: NodeJS.Timer | undefined;
    private callCheckOrderStatusesTimeout: NodeJS.Timer | undefined;
    private callCheckRenExExecutionTimeout: NodeJS.Timer | undefined;
    private callUpdateBalancesHistoryTimeout: NodeJS.Timer | undefined;

    public constructor(props: AppProps, context: object) {
        super(props, context);
        this.state = {
            checkingReLogin: false,
        };
    }

    public async componentDidMount(): Promise<void> {
        const { agreedToTerms, sdk } = this.props;

        await this.storeSDKData();

        if (!agreedToTerms) {
            this.props.actions.setPopup(newTermsPopup(this.props.actions.clearPopup));
        }

        // Check if user was logged-in already
        this.setState({ checkingReLogin: true });
        const loggedIn = await this.handleReLogin();
        if (!loggedIn) {
            this.props.actions.logout(sdk, { reload: false });
        }
        this.setState({ checkingReLogin: false });

        this.setupLoops();
    }

    public componentWillUnmount() {
        // Clear timeouts
        if (this.setupLoopsTimeout) { clearTimeout(this.setupLoopsTimeout); }
        if (this.callGetBalancesTimeout) { clearTimeout(this.callGetBalancesTimeout); }
        if (this.callHiddenOrdersTimeout) { clearTimeout(this.callHiddenOrdersTimeout); }
        if (this.callUpdatePricesTimeout) { clearTimeout(this.callUpdatePricesTimeout); }
        if (this.callLookForLogoutTimeout) { clearTimeout(this.callLookForLogoutTimeout); }
        if (this.callConnectToAtomTimeout) { clearTimeout(this.callConnectToAtomTimeout); }
        if (this.callUpdateAtomStatusTimeout) { clearTimeout(this.callUpdateAtomStatusTimeout); }
        if (this.callGetAtomicBalancesTimeout) { clearTimeout(this.callGetAtomicBalancesTimeout); }
        if (this.callCheckOrderStatusesTimeout) { clearTimeout(this.callCheckOrderStatusesTimeout); }
        if (this.callCheckRenExExecutionTimeout) { clearTimeout(this.callCheckRenExExecutionTimeout); }
        if (this.callUpdateBalancesHistoryTimeout) { clearTimeout(this.callUpdateBalancesHistoryTimeout); }
    }

    public setupLoops() {
        // See if the user has logged out every 5 seconds
        const callLookForLogout = async () => {
            const { sdk, wallet } = this.props;
            if (sdk.getAddress() && wallet === Wallet.MetaMask) {
                try {
                    await this.props.actions.lookForLogout(sdk);
                } catch (err) {
                    console.error(err);
                }
            }
            this.callLookForLogoutTimeout = setTimeout(callLookForLogout, 5 * 1000);
        };
        callLookForLogout().catch(console.error);

        // // Update the user's balances every 5 seconds
        // const callConnectToAtom = async () => {
        //     const { web3, address } = this.props;
        //     try {
        //         await this.props.actions.connectToAtom(web3, address);
        //     } catch (err) {
        //         // Couldn't connect
        //     }
        //     this.callConnectToAtomTimeout = setTimeout(callConnectToAtom, 60 * 1000);
        // };
        // callConnectToAtom().catch(console.error);

        // Update the user's balances every 5 seconds
        const callGetBalances = async () => {
            const { sdk, pendingAlerts } = this.props;
            if (sdk.getAddress()) {
                await this.props.actions.getBalances(sdk, pendingAlerts);
            }
            this.callGetBalancesTimeout = setTimeout(callGetBalances, 5 * 1000);
        };
        callGetBalances().catch(console.error);

        // Update the atom connection status every 60 seconds
        const callUpdateAtomStatus = async () => {
            const { sdk } = this.props;
            if (sdk !== null && sdk.getAddress() !== "") {
                try {
                    await this.props.actions.refreshAtomConnectionStatus(sdk);
                } catch (err) {
                    console.error(err);
                }
            }
            this.callUpdateAtomStatusTimeout = setTimeout(callUpdateAtomStatus, 60 * 1000);
        };
        callUpdateAtomStatus().catch(console.error);

        // Update the user's atomic balances every 5 seconds
        const callGetAtomicBalances = async () => {
            const { sdk } = this.props;
            if (sdk.getAddress() && sdk.atom.isConnected()) {
                try {
                    await this.props.actions.getAtomicBalances(sdk);
                } catch (err) {
                    console.error(err);
                }
            }
            this.callGetAtomicBalancesTimeout = setTimeout(callGetAtomicBalances, 5 * 1000);
        };
        callGetAtomicBalances().catch(console.error);

        // Retrieve new hidden orders from the Orderbook every 30 seconds
        const callHiddenOrders = async () => {
            const { sdk } = this.props;
            try {
                await this.props.actions.getHiddenOrders(sdk);
            } catch (err) {
                console.error(err);
            }
            this.callHiddenOrdersTimeout = setTimeout(callHiddenOrders, 30 * 1000);
        };
        callHiddenOrders().catch(console.error);

        // Update balance history every 10 seconds
        const callUpdateBalancesHistory = async (count: number) => {
            const { sdk, balanceHistory } = this.props;
            if (sdk.getAddress()) {
                try {
                    // See comment in `callCheckOrderStatuses` on count
                    await this.props.actions.updateBalancesHistory(sdk, balanceHistory, (count % 60 === 1));
                    count++;
                } catch (err) {
                    console.error(err);
                }
            }
            this.callUpdateBalancesHistoryTimeout = setTimeout(() => callUpdateBalancesHistory(count), 10 * 1000);
        };
        callUpdateBalancesHistory(0).catch(console.error);

        // Retrieve market prices every minute
        const callUpdatePrices = async () => {
            try {
                await this.updatePrices();
            } catch (err) {
                console.error(err);
            }
            this.callUpdatePricesTimeout = setTimeout(callUpdatePrices, 60 * 1000);
        };
        callUpdatePrices().catch(console.error);

        // Check for status updates every 5 seconds
        const callCheckOrderStatuses = async (count: number) => {
            const { sdk, traderOrders } = this.props;
            if (sdk.getAddress()) {
                try {
                    // Every 120 times (10 minutes) including the second time,
                    // check all orders. The full check is done on the second
                    // call so that statuses likely to change are checked
                    // sooner (in the first call) but the full check is still
                    // run early on (in the second call).
                    // Successive full checks are called every 10 minutes in
                    // case the window is not being refreshed.
                    await this.props.actions.checkOrderStatuses(sdk, traderOrders, (count % 120 === 1));
                    count += 1;
                } catch (err) {
                    console.error(err);
                }
            }
            this.callCheckOrderStatusesTimeout = setTimeout(() => callCheckOrderStatuses(count), 5 * 1000);
        };
        callCheckOrderStatuses(0).catch(console.error);
    }

    public withAccount<T extends React.ComponentClass>(component: T): React.ComponentClass | React.StatelessComponent {
        const { address } = this.props;
        const { checkingReLogin } = this.state;

        // Show a loading spinner if retrieving the web3 instance is taking a
        // while (for example, when requesting MetaMask access)
        if (checkingReLogin) {
            return () => <LoggingOut />;
        }

        if (!address) {
            const pathname = window.location.pathname;
            if (pathname !== "/" && pathname !== "/loading") {
                this.props.actions.setAlert({
                    alert: new Alert({ message: "You are not logged in. Please select your wallet to continue", alertType: AlertType.Warning })
                });
                this.props.actions.storeURL(pathname);
            }
            return () => <Redirect to="/" />;
        }
        return component;
    }

    public render(): JSX.Element {
        return (
            <Router history={history}>
                <div className="app">
                    <ScrollToTop />
                    <Route path="/" exact component={Home} />
                    <Route path="/exchange" component={this.withAccount(Exchange)} />
                    <Route path="/orders" component={this.withAccount(Orders)} />
                    <Route path="/balances" component={this.withAccount(Balances)} />
                    <Route path="/loading" component={LoggingOut} />
                    <Alerts />
                    <Popup />
                </div>
            </Router>
        );
    }

    private async updatePrices(): Promise<void> {
        for (const pair of Pairs.toArray()) {
            const [price, percentChange] = await getPrice(pair.quoteToken, pair.baseToken);
            this.props.actions.updatePrices({ price, percentChange, pair: pair.code });
        }
    }

    private async storeSDKData(): Promise<void> {
        const { sdk } = this.props;
        return Promise.all([
            sdk.fetchSupportedTokens(),
            sdk.fetchSupportedAtomicTokens(),
            sdk.fetchDarknodeFeePercent(),
            sdk.fetchWrappingFeePercent(),
            sdk.fetchMinEthTradeVolume(),
            sdk.fetchMarkets(),
        ]).then(([supportedTokens, supportedAtomicTokens, darknodeFeePercent, wrappingFeePercent, minEthTradeVolume, supportedMarkets]) => {
            this.props.actions.storeSDKData({
                supportedTokens: List(supportedTokens),
                supportedAtomicTokens: List(supportedAtomicTokens),
                darknodeFeePercent,
                wrappingFeePercent,
                minEthTradeVolume,
                supportedMarkets: List(supportedMarkets),
            });
        });
    }

    private async handleReLogin(): Promise<boolean> {
        const { address, actions, sdk, url } = this.props;
        if (address) {
            let provider;
            try {
                provider = await getInjectedWeb3Provider();
            } catch (error) {
                // Injected Web3 request was denied
                return false;
            }

            const included = await includesAddress(new Web3(provider), address);
            if (included) {
                // These are repeated in login, but the page will log-out if the
                // address is not available immediately
                sdk.updateProvider(provider);
                sdk.setAddress(address);
                actions.login(sdk, provider, address, { redirect: false, url });
                return true;
            }
        }
        return false;
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        address: state.trader.address,
        agreedToTerms: state.trader.agreedToTerms,
        balanceHistory: state.balanceHistory.balanceHistory,
        pendingAlerts: state.alert.pendingAlerts,
        sdk: state.sdk.sdk,
        traderOrders: state.traderBook.traderOrders,
        usableAtomicBalances: state.trader.usableAtomicBalances,
        usableBalances: state.trader.usableBalances,
        wallet: state.trader.wallet,
        url: state.trader.url,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: AppProps["actions"] } {
    return {
        actions: bindActionCreators({
            checkOrderStatuses,
            clearPopup,
            getAtomicBalances,
            getBalances,
            getHiddenOrders,
            login,
            logout,
            lookForLogout,
            refreshAtomConnectionStatus,
            setAlert,
            setPopup,
            storeSDKData,
            storeURL,
            updateBalancesHistory,
            updatePrices,
        }, dispatch)
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
