import { createStandardAction } from "typesafe-actions";

import { Pair } from "../../lib/market";

/**
 * Updates the market price for a specific token
 * @param  {{ price: number, pair: Pair }} payload
 */
interface UpdatePricesPayload { price: number; percentChange: number; pair: Pair; }
export type UpdatePricesAction = (payload: UpdatePricesPayload) => void;
export const updatePrices = createStandardAction("UPDATE_PRICES")<UpdatePricesPayload>();
