import { ActionType, getType } from "typesafe-actions";

import * as balancesActions from "../../actions/balances/balancesActions";

import { BalanceHistory } from "../../reducers/types";

type BalancesAction = ActionType<typeof balancesActions>;

export default function balancesReducer(state: BalanceHistory = new BalanceHistory(), action: BalancesAction) {
    switch (action.type) {
        case getType(balancesActions.storeBalanceActions):
            let balanceHistory = state.balanceHistory;

            action.payload.map((balanceAction) => {
                balanceHistory = balanceHistory.set(balanceAction.txHash, balanceAction);
            });

            // updateIndex is incremented to ensure that the BalanceHistory is
            // re-rendered.
            return state
                .set("balanceHistory", balanceHistory)
                .set("updateIndex", state.updateIndex + 1);
        default:
            return state;
    }
}
