import { ActionType, getType } from "typesafe-actions";

import * as sdkActions from "../../actions/sdk/sdkActions";

import { SDKData } from "../../reducers/types";

export type SDKAction = ActionType<typeof sdkActions>;

export default function sdkReducer(state: SDKData = new SDKData(), action: SDKAction) {
    switch (action.type) {
        case getType(sdkActions.storeSDK):
            console.assert(action.payload.sdk !== undefined);
            window.sdk = action.payload.sdk;
            return state.set("sdk", action.payload.sdk);
        case getType(sdkActions.storeSDKData):
            return state
                .set("supportedTokens", action.payload.supportedTokens)
                .set("supportedAtomicTokens", action.payload.supportedAtomicTokens)
                .set("darknodeFeePercent", action.payload.darknodeFeePercent)
                .set("wrappingFeePercent", action.payload.wrappingFeePercent)
                .set("minEthTradeVolume", action.payload.minEthTradeVolume)
                .set("supportedMarkets", action.payload.supportedMarkets);
        default:
            return state;
    }
}
