import BigNumber from "bignumber.js";

import * as React from "react";

import { Iterable, List, OrderedMap } from "immutable";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { bindActionCreators } from "redux";
import { Dispatch } from "redux";

import RenExSDK, { OrderSettlement, Token, TokenCode } from "@renex/renex";

import { setBalance, SetBalanceAction } from "../actions/inputs/inputsActions";
import { balanceToReadable } from "../lib/balances";
import { Pair, Pairs, RenExAtomicTokens, RenExTokens, TokenDetails, UnknownToken } from "../lib/market";
import { ApplicationData, BalanceInputs } from "../reducers/types";

interface StoreProps {
    sdk: RenExSDK;
    market: Pair;
    supportedTokens: List<TokenCode>;
    supportedAtomicTokens: List<TokenCode>;
    balanceInputs: BalanceInputs;
    usableBalances: OrderedMap<TokenCode, BigNumber | null>;
    usableAtomicBalances: OrderedMap<TokenCode, BigNumber | null>;
}

interface BalanceBannerProps extends StoreProps {
    actions: {
        setBalance: SetBalanceAction;
    };
}

interface BalanceBannerState {
}

/**
 * BalanceBanner is a visual component for displaying user balances in a banner
 * format
 */
class BalanceBanner extends React.Component<BalanceBannerProps, BalanceBannerState> {
    public render(): JSX.Element {
        const { sdk, market, usableBalances, usableAtomicBalances, supportedTokens, supportedAtomicTokens } = this.props;
        const atomic = Pairs.get(market).orderSettlement === OrderSettlement.RenExAtomic;
        const tokens = RenExTokens(supportedTokens);
        const atomicTokens = RenExAtomicTokens(supportedAtomicTokens);
        return (
            <div className="banner">
                <div className="container">
                    <div className="banner--details">
                        {atomic && sdk.atom.isConnected() ?
                            <>
                                <span>Swapperd balances:</span>
                                <ul>
                                    {this.listBalances(atomicTokens.toArray(), usableAtomicBalances)}
                                </ul>
                            </>
                            :
                            <>
                                <span>Your balances:</span>
                                <ul>
                                    {this.listBalances(tokens.toArray(), usableBalances)}
                                </ul>
                            </>
                        }
                    </div>
                </div>
            </div>
        );
    }

    private listBalances(tokens: TokenCode[], balances: Iterable<TokenCode, BigNumber | null>): JSX.Element[] {
        const list: JSX.Element[] = [];
        for (const token of tokens) {
            const balance = balances.get(token, null);
            const balanceText = (balance !== null) ? balance.toFixed() : "-";

            const { symbol, icon } = TokenDetails.get(token, UnknownToken);
            const image = require(`../tokens/${icon}`);

            list.push(
                <li key={token}>
                    <Link to="/balances" onClick={this.handleClick.bind(this, token)}>
                        <img alt="" role="presentation" src={image} /> {balanceText} {symbol}
                    </Link>
                </li>
            );
        }
        return list;
    }

    private handleClick(currency: TokenCode): void {
        // Update currency selection on Balances page.
        const { depositAmount, withdrawAmount } = this.props.balanceInputs;
        const balanceInputs: BalanceInputs = { currency, depositAmount, withdrawAmount };
        if (currency && currency !== this.props.balanceInputs.currency) {
            this.props.actions.setBalance({ balanceInputs });
        }
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        sdk: state.sdk.sdk,
        supportedTokens: state.sdk.supportedTokens,
        supportedAtomicTokens: state.sdk.supportedAtomicTokens,
        market: state.inputs.market,
        balanceInputs: state.inputs.balanceInputs,
        usableBalances: state.trader.usableBalances,
        usableAtomicBalances: state.trader.usableAtomicBalances,
    };
}

function mapDispatchToProps(dispatch: Dispatch): { actions: BalanceBannerProps["actions"] } {
    return {
        actions: bindActionCreators({
            setBalance,
        }, dispatch)
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(BalanceBanner);
