import * as React from "react";

import BigNumber from "bignumber.js";

import { OrderInputs, OrderSide, SimpleConsole, Token } from "@renex/renex";
import { List } from "immutable";
import { connect } from "react-redux";

import { SetPopupPayload } from "../../actions/popup/popupActions";
import { Console } from "../../components/Console";
import { TradeTokenBanner } from "../../components/TradeTokenBanner";
import { TokenDetails, UnknownToken } from "../../lib/market";
import { ErrorCanceledByUser } from "../../lib/wallets/wallet";
import { ApplicationData } from "../../reducers/types";

interface StoreProps {
    feePercent: BigNumber;
}

interface OpenOrderPopupProps extends StoreProps {
    call: (simpleConsole: SimpleConsole) => Promise<void>;
    closePopup: () => void;
    orderInputs: OrderInputs;
    skipConfirm?: boolean;
}

interface OpenOrderPopupState {
    confirmed: boolean;
    error: Error | null;
    logCount: number;
    logs: List<JSX.Element>;
}

/**
 * OpenOrderPopup is a popup component that prompts the user to approve a
 * series of Ethereum transactions
 */
class InnerOpenOrderPopup extends React.Component<OpenOrderPopupProps, OpenOrderPopupState> {
    private consoleBottom: HTMLDivElement | null = null;

    constructor(props: OpenOrderPopupProps) {
        super(props);
        this.state = {
            confirmed: props.skipConfirm !== undefined && props.skipConfirm,
            error: null,
            logCount: 0,
            logs: List(),
        };
    }

    public async componentDidMount(): Promise<void> {
        if (this.state.confirmed) {
            await this.onConfirm();
        }
    }

    public render(): JSX.Element {
        const { confirmed } = this.state;

        let inner;
        if (!confirmed) {
            inner = this.renderConfirm();
        } else {
            inner = this.renderOpening();
        }

        return <div className="popup open">
            {inner}
        </div>;
    }

    private renderConfirm = (): JSX.Element => {
        const { feePercent, orderInputs } = this.props;

        const { symbol, side, price, volume, minVolume } = orderInputs;

        const [baseToken, quoteToken] = symbol.split("/");
        const spendToken = side === OrderSide.BUY ? quoteToken : baseToken;
        const receiveToken = side === OrderSide.BUY ? baseToken : quoteToken;
        const spendTokenDetails = TokenDetails.get(spendToken, UnknownToken);
        const receiveTokenDetails = TokenDetails.get(receiveToken, UnknownToken);
        const baseTokenDetails = TokenDetails.get(baseToken, UnknownToken);
        const quoteTokenDetails = TokenDetails.get(quoteToken, UnknownToken);

        const baseVolume = new BigNumber(volume);
        const minBaseVolume = new BigNumber(minVolume || 0);
        const quoteVolume = baseVolume.times(new BigNumber(price));

        const spendVolume = (side === OrderSide.BUY) ? quoteVolume : baseVolume;
        let receiveVolume = (side === OrderSide.BUY) ? baseVolume : quoteVolume;

        // Calculate fees
        const feeToken = receiveToken;
        const feeVolume = receiveVolume.times(feePercent);
        receiveVolume = receiveVolume.minus(feeVolume);

        const feeTokenDetails = TokenDetails.get(feeToken, UnknownToken);

        return <>
            <h2>Confirm order details</h2>
            <TradeTokenBanner
                spendToken={spendTokenDetails}
                receiveToken={receiveTokenDetails}
                spendVolume={spendVolume}
                receiveVolume={receiveVolume}
            />
            <div className="open--details">
                <p><span>Price:</span> {price} {quoteTokenDetails.symbol}</p>
                <p><span>Minimum Volume:</span> {minBaseVolume.toFixed()} {baseTokenDetails.symbol}</p>
                <p>Includes a {feeVolume.toFixed()} {feeTokenDetails.symbol} fee</p>
            </div>
            <div className="popup--buttons">
                <button className="open--confirm" onClick={this.onConfirm}><span>Confirm</span></button>
                <button onClick={this.props.closePopup}>Cancel</button>
            </div>
        </>;
    }

    private renderOpening = (): JSX.Element => {
        const { error, logs } = this.state;
        const { closePopup } = this.props;

        let title = <h2>Opening order...</h2>;
        let message = <p>Please wait. Do not close this window.</p>;
        if (error) {
            switch (error.message) {
                case ErrorCanceledByUser:
                    title = <h2>Order canceled</h2>;
                    message = <p />;
                    break;
                default:
                    title = <h2 className="red">Unable to complete transaction</h2>;
                    message = <p>{error.message}</p>;
                    break;
            }
        }
        return <>
            {title}
            {message}
            {error &&
                <div className="popup--buttons">
                    <button onClick={closePopup}>Close</button>
                </div>
            }
            <Console logs={logs} />
        </>;
    }

    private onConfirm = async () => {
        const { call } = this.props;

        this.setState({ confirmed: true });

        const simpleConsole = { log: this.log, error: this.error };

        await call(simpleConsole)
            .catch((error) => {
                console.error(error);
                // Set state may fail if unmounted
                try {
                    this.setState({ error });
                } catch (err) {
                    console.error(err);
                }
            });
    }

    private log = (message: string) => {
        const logCount = this.state.logCount;
        this.setState({ logCount: logCount + 1, logs: this.state.logs.push(<p key={logCount}>{message}</p>) });
    }

    private error = (message: string) => {
        const logCount = this.state.logCount;
        this.setState({ logCount: logCount + 1, logs: this.state.logs.push(<p key={logCount} className="red">{message}</p>) });
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        feePercent: state.sdk.darknodeFeePercent,
    };
}

export const OpenOrderPopup = connect(mapStateToProps, null)(InnerOpenOrderPopup);

export const newOpenOrderPopup = (orderInputs: OrderInputs, call: (simpleConsole?: SimpleConsole) => Promise<void>, onCancelAction: () => void, skipConfirm?: boolean): SetPopupPayload => ({
    popup: <OpenOrderPopup call={call} closePopup={onCancelAction} orderInputs={orderInputs} skipConfirm={skipConfirm} />,
    dismissible: false,
    onCancel: onCancelAction,
});
