import { ActionType, getType } from "typesafe-actions";

import * as inputsActions from "../../actions/inputs/inputsActions";

import { InputsData } from "../../reducers/types";

export type InputsAction = ActionType<typeof inputsActions>;

export default function inputsReducer(state: InputsData = new InputsData(), action: InputsAction) {
    switch (action.type) {
        case getType(inputsActions.setMarket):
            return state.set("market", action.payload.market);
        case getType(inputsActions.setOrder):
            return state.set("orderInputs", action.payload.orderInputs);
        case getType(inputsActions.setBalance):
            return state.set("balanceInputs", action.payload.balanceInputs);
        default:
            return state;
    }
}
