import * as React from "react";

import { OrderedMap } from "immutable";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import RenExSDK, { TraderOrder } from "@renex/renex";

import Heading from "./Heading";

import { TokenDetails, UnknownToken } from "../lib/market";
import { ApplicationData } from "../reducers/types";
import { RECENT_AMOUNT } from "./SubmittedTrades";

interface StoreProps {
    traderOrders: OrderedMap<string, TraderOrder>;
    sdk: RenExSDK;
}

interface ExecutedTradesProps extends StoreProps {
    recent?: boolean;
}

interface ExecutedTradesState {
    relevantRows: number;
}

/**
 * ExecutedTrades is a visual component for displaying the execution details of
 * a user's executed trades
 */
class ExecutedTrades extends React.Component<ExecutedTradesProps, ExecutedTradesState> {
    public constructor(props: ExecutedTradesProps, context: object) {
        super(props, context);
        this.state = {
            relevantRows: 0
        };
    }

    public render(): JSX.Element {
        const { sdk, traderOrders } = this.props;

        const executedOrders = traderOrders
            .toArray()
            .filter((traderOrder: TraderOrder) => {
                if (!traderOrder.matchDetails) {
                    return false;
                }

                if (traderOrder.trader.toLowerCase() !== sdk.getAddress().toLowerCase()) {
                    return false;
                }

                return true;
            });

        const recent: boolean = (this.props.recent === true);

        let relevantRows: JSX.Element[] | null = null;
        if (executedOrders.length > 0) {
            relevantRows = this.generateRows(executedOrders);
        }

        const title = `Your ${recent ? "latest " : ""}executed orders`;

        return (
            <div className="section trades">
                <div className="container">
                    <Heading title={title} />
                    <div className="table--wrapper">
                        <table>
                            <thead>
                                <tr>
                                    {/* If any fields are added/removed, the colSpan
                                    below must be modified to reflect this. */}
                                    <th>Order ID</th>
                                    <th>Match ID</th>
                                    {/* <th>Match Price</th> */}
                                    <th>Spent Amount</th>
                                    <th>Receive Amount</th>
                                </tr>
                            </thead>
                            <tbody>
                                {relevantRows !== null ?
                                    recent ? relevantRows.slice(0, RECENT_AMOUNT) : relevantRows
                                    :
                                    <tr>
                                        <td colSpan={4}>No executed orders</td>
                                    </tr>
                                }
                            </tbody>
                        </table>
                    </div>
                    {recent && relevantRows !== null && relevantRows.length - RECENT_AMOUNT > 0 ?
                        <p><Link to="/orders">View {relevantRows.length - RECENT_AMOUNT} more</Link></p> :
                        null
                    }
                </div>
            </div>
        );
    }

    private generateRows(executedOrders: TraderOrder[]): JSX.Element[] | null {
        return executedOrders
            .reverse()
            .map((order: TraderOrder, key: number) => {
                const matchDetails = order.matchDetails;

                if (matchDetails === undefined) {
                    return <></>;
                }

                const receivedTokenDetails = TokenDetails.get(matchDetails.receivedToken, UnknownToken);
                const spentTokenDetails = TokenDetails.get(matchDetails.spentToken, UnknownToken);

                const receiveAmountReadable = matchDetails.receivedVolume;
                const paidAmountReadable = matchDetails.spentVolume.plus(matchDetails.fee);
                // const feeReadable = balanceToReadable(matchDetails.fee, matchDetails.spentToken);

                return <tr key={key}>
                    <td><span className="monospace">{matchDetails.orderID}</span></td>
                    <td className="match"><span className="monospace">{matchDetails.matchedID}</span></td>
                    {/* <td className="center price"><span>{`${pricepoint} ${fstSymbol} to ${sndSymbol}`}</span></td> */}
                    <td className="center volume"><span>{`${paidAmountReadable} ${spentTokenDetails.symbol}`}</span></td>
                    <td className="center volume"><span>{`${receiveAmountReadable} ${receivedTokenDetails.symbol}`}</span></td>
                </tr>;
            });
    }
}

function mapStateToProps(state: ApplicationData): StoreProps {
    return {
        traderOrders: state.traderBook.traderOrders,
        sdk: state.sdk.sdk,
    };
}

export default connect(mapStateToProps)(ExecutedTrades);
